

define('pages/demographics',['backbone', 'Handlebars', 'text!./demographics.hbs', 'ui_widgets/formation', './demographics-fields', 'core'], function(Backbone, Handlebars, tmpl, Formation, demographicsFields, app) {
  var Me, _formRegionSel, _groups, _letsGoBtnMarkup, _startBtnSel, _wrapperMarkup;
  _formRegionSel = '#FormRegion';
  _startBtnSel = '#ActionStartGame';
  _letsGoBtnMarkup = '\
    <a id="ActionStartGame" class="btn btn-large btn-primary btn-alt pull-right" href="#game">Save My Info and Let\'s Go</a>\
  ';
  _wrapperMarkup = '<div class="col3rds"></div>';
  _groups = ['.dob, .education', '.handedness', '.gender'];
  Me = Backbone.View.extend({
    title: 'Demographics',
    className: 'demographicsPage',
    events: {
      'change input': 'onChangeInput',
      'change select': 'onChangeInput',
      'click #ActionStartGame': 'onClickContinue'
    },
    initialize: function() {
      return this.formation = new Formation({
        data: demographicsFields,
        values: app.user.attributes
      });
    },
    render: function() {
      this.$el.html(tmpl);
      this.$(_formRegionSel).html(this.formation.render().el);
      return this;
    },
    onDomInsert: function() {
      return this._wrapGroups();
    },
    _wrapGroups: function() {
      var group, _i, _len, _results;
      _results = [];
      for (_i = 0, _len = _groups.length; _i < _len; _i++) {
        group = _groups[_i];
        _results.push($(group).wrapAll(_wrapperMarkup));
      }
      return _results;
    },
    onChangeInput: function() {
      if (this._alreadyChanged) {
        return;
      }
      this.$(_startBtnSel).replaceWith(_letsGoBtnMarkup);
      return this._alreadyChanged = true;
    },
    onClickContinue: function() {
      var demographics, jxhr;
      demographics = this.formation.getVals();
      if (!_.isEmpty(demographics)) {
        app.user.set(demographics);
        if (!app.user.isNew()) {
          return jxhr = app.user.save;
        }
      }
    }
  });
  return Me;
});
