

define('ui_widgets/hold_please',['jquery', 'backbone', 'Handlebars', 'text!./hold_please.hbs', 'utils/numbers'], function($, Backbone, Handlebars, tmpl, numbers) {
  var Me, _loadingMessages, _me, _msgHolderSel;
  _me = 'ui_widgets/hold_please';
  _msgHolderSel = '.msg';
  _loadingMessages = ['Powering up the Flux Capacitor...', 'Seeking the Keymaster...', 'Venturing in the jungle for the golden statue...', 'Seeking the lost ark...', 'Finding the secret plans...', 'Wax on, wax off...', 'Recieving signal from a galaxy far, far away...', 'Seeking the sourthern oracle...', 'Evading the nothing...', 'Microwaving the potato...', 'Getting on the highway to the danger zone...', 'Pulling baby out of the corner...', 'Phoning home...', 'Getting loose. Footloose...', 'Feeding Seymore...', 'Just a jump the the left and a step to the right. More to go...'];
  Me = Backbone.View.extend({
    tmpl: Handlebars.compile(tmpl),
    tagName: 'b',
    className: 'holdPlease',
    events: {
      "click": "_clicked"
    },
    initialize: function() {},
    render: function(iconSize) {
      this.$el.html(this.tmpl({
        iconSize: iconSize
      }));
      return this;
    },
    _clicked: function(e) {
      e.preventDefault();
      return e.stopPropagation();
    },
    _addMsg: function(msg) {
      if (msg) {
        if (msg === true) {
          msg = numbers.pickOneAnyOne(_loadingMessages);
        }
        return $("." + this.className + " " + _msgHolderSel).text(msg);
      }
    },
    _showOnSelector: function(selector, iconSize) {
      if ($(selector).hasClass('btn-large')) {
        iconSize = 'icon-large';
      }
      return $(selector).addClass('onHold').css('position', 'relative').append(this.render(iconSize).$el.clone(true));
    },
    _showFullScreen: function() {
      return this._showOnSelector('body', 'icon-4x');
    },
    _hideInSelector: function(selector) {
      return $(selector).removeClass('onHold').css('position', '').find("." + this.className).remove();
    },
    _hideAll: function() {
      $("." + this.className).parent().removeClass('onHold').css('position', '');
      return $("." + this.className).remove();
    },
    show: function(selector, msg) {
      if (selector) {
        this._showOnSelector(selector);
      } else {
        this._showFullScreen();
      }
      return this._addMsg(msg);
    },
    hide: function(selector) {
      if (selector) {
        return this._hideInSelector(selector);
      } else {
        return this._hideAll();
      }
    }
  });
  return new Me();
});
