

define('game/levels/rank_images/main',['backbone', 'Handlebars', 'game/levels/_base', 'text!./main.hbs', './rankable_images', 'jqueryui/sortable'], function(Backbone, Handlebars, Level, tmpl, RankableImages, xJqUISortable) {
  var View, _EVENTS, _me, _msgHtml, _proceedSel, _rankableImageSel, _rankingSel, _sortableSel, _unrankedSel;
  _me = 'game/levels/rank_images';
  _rankingSel = '#RankingArea';
  _unrankedSel = '#UnrankedArea';
  _rankableImageSel = '.rankableImage';
  _proceedSel = '.proceed';
  _sortableSel = '.connectedSortable';
  _msgHtml = '<li class="message">Rank from favorite to least favorite by clicking or dragging images.</li>';
  _EVENTS = {
    ranked: "image_ranked",
    unranked: "image_rank_cleared"
  };
  View = Level.extend({
    className: 'rankImages',
    start: function() {
      this.collection = new RankableImages(this.model.get('image_sequence'));
      this.options.instructions.set({
        text: this.model.get('instructions')
      });
      _.bindAll(this, 'onOver', 'onSortStart', 'onSortEnd', 'onUnrankedImageClick', 'onRankedImageClick', 'onUnrankedImageKeypress', 'onRankedImageKeypress');
      this.listenTo(this.collection, 'change:rank', this.onRankChange);
      return this.track(Level.EVENTS.start, {
        data: this.collection.toJSON()
      });
    },
    render: function() {
      var _this = this;
      this.$el.html(tmpl);
      require(['jquiTouchPunch'], function() {
        _this.collection.each(function(imageModel) {
          return _this.$(_unrankedSel).append(imageModel.view.render().el);
        });
        _this.$msg = $(_msgHtml);
        _this._checkOnMsg();
        _this.$(_sortableSel).sortable({
          containment: "." + _this.className,
          placeholder: 'rankableImage placeholder',
          connectWith: _sortableSel,
          cancel: "." + (_this.$msg.prop('class')),
          over: _this.onOver,
          start: _this.onSortStart,
          stop: _this.onSortEnd
        });
        _this.$(_sortableSel).disableSelection();
        _this.$(_unrankedSel).on('keypress', '.rankableImage', _this.onUnrankedImageKeypress);
        _this.$(_rankingSel).on('keypress', '.rankableImage', _this.onRankedImageKeypress);
        if (!(navigator.userAgent.toLowerCase().indexOf('firefox') > -1)) {
          _this.$(_unrankedSel).on('click', '.rankableImage', _this.onUnrankedImageClick);
          return _this.$(_rankingSel).on('click', '.rankableImage', _this.onRankedImageClick);
        }
      });
      return this;
    },
    _checkOnMsg: function() {
      if (this.$(_rankingSel).children().length) {
        return this._hideMsg();
      } else {
        return this._showMsg();
      }
    },
    _showMsg: function() {
      return this.$(_rankingSel).append(this.$msg);
    },
    _hideMsg: function() {
      return this.$msg.remove();
    },
    _checkOnRanks: function() {
      var rankedImages;
      this.collection.each(function(model) {
        if (model.view.$el.parent(_rankingSel).length) {
          return model.set({
            rank: model.view.$el.index()
          });
        } else {
          return model.set({
            rank: model.defaults.rank
          });
        }
      });
      this._buildRankArrayForServer();
      rankedImages = this.collection.filter(function(image) {
        var rank;
        rank = image.attributes.rank;
        if (rank || rank === 0) {
          return true;
        }
      });
      if (rankedImages.length === this.collection.length) {
        return this.readyToProceed();
      } else {
        return this.notReadyToProceed();
      }
    },
    _buildRankArrayForServer: function() {
      return this.summaryData = {
        final_rank: this.collection.pluck('rank')
      };
    },
    onUnrankedImageKeypress: function(e) {
      var $firstImg;
      if (!(e.charCode || e.which === 32)) {
        return;
      }
      this.onUnrankedImageClick(e);
      $firstImg = this.$(_unrankedSel).find(_rankableImageSel).first();
      if ($firstImg) {
        return $firstImg.focus();
      }
    },
    onRankedImageKeypress: function(e) {
      if (!(e.charCode || e.which === 32)) {
        return;
      }
      return this.onRankedImageClick(e);
    },
    onUnrankedImageClick: function(e) {
      this.$(_rankingSel).append(e.currentTarget);
      this._checkOnMsg();
      return this._checkOnRanks();
    },
    onRankedImageClick: function(e) {
      this.$(_unrankedSel).append(e.currentTarget);
      this._checkOnMsg();
      return this._checkOnRanks();
    },
    onOver: function() {
      return this._checkOnMsg();
    },
    onSortStart: function(e, ui) {
      var id;
      id = $(e.currentTarget).find('img').data('id');
      return this._trackDragged(id);
    },
    onSortEnd: function(e, ui) {
      return this._checkOnRanks();
    },
    onRankChange: function(model, value, options) {
      if (value === null) {
        return this._trackRankCleared(model.attributes.image_id, model.previous('rank'));
      } else {
        return this._trackRanked(model.attributes.image_id, value);
      }
    },
    _trackRanked: function(id, newRank) {
      return this.track(_EVENTS.ranked, {
        image_no: id,
        rank: newRank
      });
    },
    _trackRankCleared: function(id, oldRank) {
      return this.track(_EVENTS.unranked, {
        image_no: id,
        prev_rank: oldRank
      });
    },
    _trackDragged: function(id) {
      return this.track(Level.EVENTS.moveStart, {
        image_no: id
      });
    }
  });
  return View;
});

define('game/levels/rank_images', ['game/levels/rank_images/main'], function (main) { return main; });
