

define('game/levels/proximity_takes_turns/main',['jquery', 'underscore', 'backbone', 'entities/circles', 'game/levels/_base', 'text!./proximity_takes_turns.hbs', './billboard', './self_view', './circle_view', 'ui_widgets/proceed'], function($, _, Backbone, Circles, Level, tmpl, Billboard, SelfView, CircleView, proceed) {
  var View, _downKey, _selfContentSel, _upKey;
  _selfContentSel = '.zoneSelf';
  _upKey = 38;
  _downKey = 40;
  View = Level.extend({
    className: 'proximityTakesTurns',
    initialize: function() {
      this.listenTo(this.collection, 'change:interacted', this.onChangeInteracted);
      this.listenTo(this.collection, 'change:focus', this.onChangeFocus);
      this.listenTo(this.collection, 'change:selfProximityPx', this.onChangeSelfProximityPx);
      this.listenToOnce(proceed, 'click', this._finish);
      this.once('domInsert', this.fillHeight);
      _.bindAll(this, 'next', 'onKeydown');
      return $(window).on('keydown', this.onKeydown);
    },
    render: function() {
      this.$el.html(tmpl);
      this.billboard = new Billboard;
      this.$el.append(this.billboard.render().el);
      this.selfView = new SelfView;
      this.$(_selfContentSel).append(this.selfView.render().el);
      this._renderCircles();
      this.next();
      return this;
    },
    _renderCircles: function() {
      var _this = this;
      this.collection.each(function(circle) {
        circle.view = new CircleView({
          model: circle,
          selfView: _this.selfView,
          track: _.bind(_this.options.runner.track, _this.options.runner)
        });
        return _this.$el.append(circle.view.render().el);
      });
      return this;
    },
    _finish: function() {
      this.collection.each(function(model) {
        return model.view.remove();
      });
      proceed.hide();
      this.trigger('done');
      this.remove();
      return this;
    },
    _clearFocus: function() {
      this.collection.invoke('set', {
        focus: false
      });
      return this.billboard.focusOut();
    },
    _focusCircle: function(circle) {
      this._clearFocus();
      circle.set({
        focus: true
      }, {
        dontBubble: true
      });
      if (circle.attributes.interacted) {
        this.billboard.focusIn(circle);
      } else {
        this.billboard.slideIn(circle);
      }
      return this;
    },
    onKeydown: function(event) {
      var code, curCircle;
      code = event.charCode || event.which;
      curCircle = this.collection.find(function(circle) {
        return circle.attributes.focus;
      });
      console.log('keydown');
      if (!curCircle) {
        return;
      }
      switch (code) {
        case _upKey:
          curCircle.view.bumpUp();
          break;
        case _downKey:
          curCircle.view.bumpDown();
      }
      return this;
    },
    onChangeInteracted: function() {
      if (!this.allMoved) {
        return this.next();
      }
    },
    onChangeFocus: function(model, isFocused, options) {
      if (options.dontBubble) {
        return;
      }
      if (isFocused) {
        return this._focusCircle(model);
      }
    },
    onChangeSelfProximityPx: function() {
      var distances, max, ratio;
      distances = this.collection.pluck('selfProximityPx');
      max = _.max(distances);
      ratio = max / 100;
      return this.collection.each(function(model) {
        var normalized;
        if (model.attributes.selfProximityPx != null) {
          normalized = Math.round(model.attributes.selfProximityPx / ratio);
          return model.set({
            selfProximityNormalized: normalized
          });
        } else {
          return model.set({
            selfProximityNormalized: null
          });
        }
      });
    },
    next: function() {
      var firstModel, fresh;
      fresh = this.collection.filter(function(item) {
        return !item.get('interacted');
      });
      firstModel = fresh[0];
      if (firstModel) {
        firstModel.set({
          focus: true
        });
      } else {
        this.allMoved = true;
        this._clearFocus();
        proceed.show();
      }
      return this;
    },
    close: function() {
      $(window).off('keydown', this.onKeydown);
      return this;
    }
  });
  return View;
});

define('game/levels/proximity_takes_turns', ['game/levels/proximity_takes_turns/main'], function (main) { return main; });
