

define('dashboard/widgets/lister/main',['backbone', 'Handlebars', 'text!./lister.hbs', 'dashboard/widgets/base'], function(Backbone, Handlebars, tmpl, Widget) {
  var View, _tmpl;
  _tmpl = Handlebars.compile(tmpl);
  View = Widget.extend({
    tagName: 'section',
    renderList: function(data) {
      if (!(data.list && data.title)) {
        console.error("Need data.list and data.title");
      }
      this.$el.html(_tmpl(data));
      return this;
    }
  });
  return View;
});

define('dashboard/widgets/lister', ['dashboard/widgets/lister/main'], function (main) { return main; });
