

define('dashboard/emotions/strongest_emotions',['backbone', 'dashboard/widgets/base', 'game/results/emotions_overview'], function(Backbone, Widget, EmotionsOverviewResult) {
  var View, _chartName, _widgetSel;
  _widgetSel = '.widget';
  _chartName = 'Primary Emotions';
  View = Widget.extend({
    events: {
      'click .widget': 'onClick'
    },
    className: 'chart-primaryEmotions',
    render: function() {
      this.$el.html(this.tmplBase({
        title: _chartName,
        className: 'pressable'
      }));
      this.emotionsOverviewResult = new EmotionsOverviewResult({
        collection: this.collection
      });
      this.$(_widgetSel).append(this.emotionsOverviewResult.el);
      this.emotionsOverviewResult.renderChart();
      return this;
    },
    onClick: function() {
      return this.emotionsOverviewResult.$el.trigger('click');
    },
    close: function() {
      var _ref;
      if ((_ref = this.emotionsOverviewResult) != null) {
        _ref.remove();
      }
      return this.remove();
    }
  });
  View.dependsOn = 'entities/results/results';
  View.fetchOptions = {
    data: {
      type: 'EmoResult'
    }
  };
  return View;
});
