

define('entities/careBears/careBear',['classes/model'], function(Model) {
  var Export, _me;
  _me = 'entities/careBears/careBear';
  Export = Model.extend({
    initialize: function() {
      this.on('error', this.onErr);
      return this.on('invalid', this.onInvalid);
    },
    sync: function() {
      var url;
      if (!this.url) {
        return;
      }
      url = "" + this.url + "?" + ($.param(this.attributes));
      return window.open(url);
    },
    onErr: function() {
      return console.error("" + _me + ": Trouble sharing");
    },
    onInvalid: function(model, err) {
      return console.error("" + _me + ": Invalid: " + err);
    }
  });
  return Export;
});
