

define('dashboard/teasers/personalizations',['backbone', 'Handlebars', 'text!./personalizations.hbs', 'dashboard/widgets/base', 'core'], function(Backbone, Handlebars, tmpl, Widget, app) {
  var View;
  View = Widget.extend({
    tmpl: tmpl,
    className: 'twilight doubleWide teaser-personalizations',
    events: {
      click: 'onClick'
    },
    onSync: function() {
      if (this.model && !this.model.attributes.data) {
        return this.render();
      } else {
        return this.remove();
      }
    },
    onClick: function() {
      app.analytics.track('Personalizations', 'Teaser Pressed');
      return app.trigger('action:showPersonalizations');
    }
  });
  View.dependsOn = 'entities/preferences/training';
  return View;
});
