

define('pages/friend_survey',['jquery', 'underscore', 'backbone', 'Handlebars', 'core', 'text!./friend_survey.hbs', 'text!./friend_survey-thanks.hbs', './friend_survey-fields', 'ui_widgets/icon_slider', 'ui_widgets/hold_please'], function($, _, Backbone, Handlebars, app, tmpl, tmplThanks, fieldData, IconSlider, holdPlease) {
  var Fields, Me, _contentSel, _submitSel;
  _contentSel = '#ContentHolder';
  _submitSel = '#SubmitForm';
  Fields = Backbone.Collection.extend({
    url: function() {
      return "" + app.cfg.apiServer + "/api/v1/games/" + this.game_id + "/friend_survey";
    },
    toJSON: function(options) {
      var nvp;
      nvp = {};
      this.each(function(model) {
        return nvp[model.attributes.question_id] = model.attributes.value;
      });
      return {
        friend_survey: nvp
      };
    },
    sync: function(options) {
      return Backbone.sync('create', this, options);
    }
  });
  Me = Backbone.View.extend({
    title: 'Help a Friend',
    className: 'friendSurveyPage',
    events: {
      'click #SubmitForm': 'onSubmit'
    },
    initialize: function() {
      _.bindAll(this, 'onSync', 'onError');
      this.collection = new Fields(fieldData);
      return this.collection.game_id = this.options.params.game_id;
    },
    render: function() {
      var _this = this;
      this.$el.html(tmpl);
      this.collection.each(function(model) {
        var question;
        question = new IconSlider({
          model: model
        });
        return _this.$(_contentSel).append(question.render().el);
      });
      return this;
    },
    onSubmit: function() {
      var promise;
      holdPlease.show(_submitSel);
      app.analytics.trackKeyMetric('Friend Survey', 'Submitted Answers');
      promise = this.collection.sync('create');
      promise.done(this.onSync);
      return promise.fail(this.onError);
    },
    onSync: function() {
      holdPlease.hide();
      return this.$el.html(tmplThanks);
    },
    onError: function() {
      holdPlease.hide();
      throw new Error('Problem saving');
    }
  });
  return Me;
});
