

define('classes/collection',['underscore', 'backbone', 'classes/model'], function(_, Backbone, Model) {
  var Collection;
  Collection = Backbone.Collection.extend({
    model: Model,
    dewrap: Model.prototype.dewrap,
    parse: Model.prototype.parse,
    mixalot: function() {
      this.reset(this.shuffle(), {
        silent: true
      });
      return this;
    }
  });
  return Collection;
});
