

define('ui_widgets/steps_remaining',['backbone', 'Handlebars', "text!./steps_remaining.hbs"], function(Backbone, Handlebars, tmpl) {
  var View, _tmpl;
  _tmpl = Handlebars.compile(tmpl);
  View = Backbone.View.extend({
    className: 'stepsRemaining',
    initialize: function() {
      if (!this.collection) {
        throw new Error('Need a collection');
      }
      return this.listenTo(this.collection, 'change:isComplete', this.render);
    },
    render: function() {
      this.$el.html(_tmpl({
        title: this.options.title,
        levels: this.collection.toJSON(),
        width: "" + (Math.floor(1 / this.collection.length * 100)) + "%"
      }));
      return this;
    },
    setComplete: function(LevelId) {
      var level;
      level = this.collection.at(LevelId);
      if (level != null) {
        return level.set('isComplete', true);
      }
    }
  });
  return View;
});
