

define('pages/dashboard/_base',['backbone', 'core', 'dashboard/widgetmaster'], function(Backbone, app, Widgetmaster) {
  var View;
  View = Backbone.View.extend({
    title: null,
    className: null,
    initialize: function() {
      this.listenTo(app.user, 'sync', this.render);
      if (!app.user.isLoggedIn()) {
        return app.trigger('session:showLogin');
      }
    },
    renderWidgets: function(widgetList) {
      this.widgetmaster = new Widgetmaster({
        widgets: widgetList
      });
      this.$el.html(this.widgetmaster.render().el);
      return this;
    }
  });
  return View;
});
