

define('entities/properties',['backbone', 'classes/model'], function(Backbone, Model) {
  var Collection, OneProperty, _TYPES, _capFirstLetter, _me;
  _me = 'entities/properities';
  _TYPES = {
    selectByIcon: 'select_by_icon',
    email: 'email',
    password: 'password',
    url: 'url',
    tel: 'tel',
    date: 'date',
    text: 'text',
    select: 'select',
    rocker: 'rocker',
    checkbox: 'checkbox'
  };
  _capFirstLetter = function(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  };
  OneProperty = Model.extend({
    defaults: {
      string_id: null,
      label: null,
      type: _TYPES.text,
      required: null,
      value: null,
      multiSelect: null
    },
    initialize: function() {}
  });
  Collection = Backbone.Collection.extend({
    model: OneProperty
  });
  Collection.TYPES = _TYPES;
  return Collection;
});
