

define('connections/connection_view',['backbone', 'Handlebars', 'text!./connection_view.hbs'], function(Backbone, Handlebars, tmpl) {
  var View, _tmpl;
  Handlebars.registerHelper('prettyDate', function(dateStr) {
    var date;
    date = new Date(dateStr);
    return date.toLocaleDateString();
  });
  Handlebars.registerHelper('prettyTime', function(dateStr) {
    var date;
    date = new Date(dateStr);
    return date.toLocaleTimeString();
  });
  _tmpl = Handlebars.compile(tmpl);
  View = Backbone.View.extend({
    className: 'connection clearfix',
    tagName: 'li',
    events: {
      'click .sync': 'onClickSync'
    },
    initialize: function() {
      return this.listenTo(this.model, 'change', this.render);
    },
    render: function() {
      this.$el.html(_tmpl(this.model.attributes));
      return this;
    },
    onClickSync: function() {
      return this.model.syncConnection();
    }
  });
  return View;
});
