

define('game/results/emotions_overview',['backbone', 'Handlebars', 'game/results/base', 'text!./emotions.hbs', 'markdown', 'charts/pie', 'composite_views/perch'], function(Backbone, Handlebars, ResultView, tmpl, markdown, PieChart, perch) {
  var View, _chartName, _max, _min;
  _chartName = 'Primary Emotions';
  _min = .2;
  _max = 5;
  View = ResultView.extend({
    className: 'emotionsOverview',
    events: {
      click: 'onClick'
    },
    render: function() {
      if (this.collection == null) {
        console.error("Need a collection");
      }
      this.listenTo(this.collection, 'sync', this.renderChart);
      return this;
    },
    renderChart: function() {
      this.chart = new PieChart({
        data: {
          name: _chartName,
          chartValues: this._prepareData(this.collection),
          options: {
            animation: false
          }
        }
      });
      this.$el.html(this.chart.render().el);
      return this;
    },
    _prepareData: function(collection) {
      var chartData, emotions, key, latestResult, normalized, tuples, value;
      latestResult = this.collection.at(this.collection.length - 1);
      emotions = latestResult.attributes.calculations.emo_distances;
      tuples = _.map(emotions, function(val, key) {
        return [key, val];
      });
      tuples = _.sortBy(tuples, function(t) {
        return t[1];
      });
      chartData = {};
      chartData[tuples[0][0]] = tuples[0][1];
      chartData[tuples[1][0]] = tuples[1][1];
      chartData[tuples[tuples.length - 1][0]] = tuples[tuples.length - 1][1];
      chartData[tuples[tuples.length - 2][0]] = tuples[tuples.length - 2][1];
      chartData[tuples[tuples.length - 3][0]] = tuples[tuples.length - 3][1];
      for (key in chartData) {
        value = chartData[key];
        normalized = _max - value;
        normalized = normalized < _min ? _min : normalized + _min;
        chartData[key] = normalized;
      }
      return chartData;
    },
    onClick: function(e) {
      e.stopPropagation();
      return perch.show({
        title: _chartName,
        large: true,
        btn1Text: null,
        content: new PieChart({
          data: {
            name: _chartName,
            chartValues: this._prepareData(this.collection),
            width: 450,
            height: 300
          }
        })
      });
    }
  });
  return View;
});
