

define('entities/results/results',['classes/collection', 'entities/results/result', 'core'], function(Collection, Result, app) {
  var Export, _me;
  _me = 'entities/results/results';
  Export = Collection.extend({
    url: function() {
      return "" + app.cfg.apiServer + "/api/v1/users/-/results";
    },
    model: Result,
    initialize: function() {
      return this.on('error', this.onErr);
    },
    onErr: function() {
      return console.error("" + _me + ": error");
    }
  });
  Export.STATES = Result.STATES;
  return Export;
});
