

define('dashboard/career/tools',['backbone', 'dashboard/widgets/lister'], function(Backbone, Lister) {
  var View, _defaults;
  _defaults = View = Lister.extend({
    className: 'coolTones',
    render: function() {
      this.renderList({
        title: 'Tools of the Trade',
        list: this.model.attributes.tools
      });
      return this;
    }
  });
  View.dependsOn = 'entities/my/career';
  return View;
});
