

define('entities/friend_survey',['underscore', 'classes/model', 'core'], function(_, Model, app) {
  var Export;
  Export = Model.extend({
    url: function() {
      return "" + app.cfg.apiServer + "/api/v1/games/" + this.attributes.game_id + "/friend_survey";
    },
    initialize: function() {
      return this.on('error', this.onErr);
    },
    onErr: function() {
      return console.error("Error getting the friend survey results");
    }
  });
  return Export;
});
