

define('core/mediator',['underscore', 'backbone', 'composite_views/perch', 'forms/user/login', 'forms/user/profile', 'preferences/personalization', 'connections/connection_list'], function(_, Backbone, perch, UserLogin, UserProfile, PersonalizationView, ConnectionList) {
  var Me, _me;
  _me = 'core/mediator';
  Me = Backbone.View.extend({
    initialize: function() {
      if (this.options.app == null) {
        throw new Error('Need options.app');
      }
      perch.options.app = this.options.app;
      return this._mediate();
    },
    _mediate: function() {
      this.listenTo(this.options.app, 'session:showLogin', this._showLogin);
      this.listenTo(this.options.app, 'session:showRegister', this._showRegister);
      this.listenTo(this.options.app, 'session:showProfile', this._showProfile);
      this.listenTo(this.options.app, 'session:logOut', this._actionLogOut);
      this.listenTo(this.options.app, 'action:showPersonalizations', this._showPersonalizations);
      return this.listenTo(this.options.app, 'action:showConnections', this._showConnections);
    },
    _showLogin: function() {
      return perch.show({
        btn1Text: null,
        supressTracking: true,
        content: new UserLogin({
          model: this.options.app.user,
          app: this.options.app
        })
      });
    },
    _showRegister: function() {
      return perch.show({
        btn1Text: null,
        supressTracking: true,
        content: new UserLogin({
          model: this.options.app.user,
          app: this.options.app,
          register: true
        })
      });
    },
    _showProfile: function() {
      return perch.show({
        title: 'Profile',
        btn1Text: null,
        content: new UserProfile({
          model: this.options.app.user
        })
      });
    },
    _showPersonalizations: function() {
      return perch.show({
        title: '<i class="icon-cog"></i> Your Preferences',
        className: 'brandHappy',
        btn1Text: null,
        huge: true,
        content: new PersonalizationView({
          app: this.options.app
        })
      });
    },
    _showConnections: function() {
      return perch.show({
        title: 'Connections',
        btn1Text: null,
        content: new ConnectionList({
          app: this.options.app
        })
      });
    },
    _actionLogOut: function() {
      this.options.app.analytics.track('session', 'Pressed Log Out');
      this.options.app.router.navigate('home', {
        trigger: true
      });
      return this.options.app.session.logOut();
    }
  });
  return Me;
});
