

define('game/results/reaction_time_history',['backbone', 'Handlebars', 'game/results/base', 'text!./reaction_time_history.hbs'], function(Backbone, Handlebars, ResultView, tmpl) {
  var View, _barSel, _holderSel, _me, _minSpeed, _scrollFixDelay;
  _holderSel = '.chartHolder';
  _barSel = '.bar';
  _scrollFixDelay = 50;
  _me = 'game/results/reaction_time_history';
  _minSpeed = 190;
  View = ResultView.extend({
    className: 'reactionTimeHistory',
    render: function() {
      if (this.collection == null) {
        console.error("Need a collection");
      }
      this.listenTo(this.collection, 'sync', this.renderChart);
      return this;
    },
    renderChart: function() {
      var _this = this;
      if (!this.collection.length) {
        this.remove();
        return;
      }
      this._compileTemplateForData();
      this.$el.html(this.tmpl({
        max: this.collection.max,
        min: this.collection.min,
        results: this.collection.toJSON()
      }));
      this._addCallbackToTooltipShow();
      this.$(_barSel).tooltip({
        placement: 'right',
        container: 'body',
        html: true,
        animation: false,
        afterShow: _.bind(this.onTooltipShow, this)
      });
      return setTimeout((function() {
        return _this.$(_holderSel).scrollLeft(9999);
      }), _scrollFixDelay);
    },
    _addCallbackToTooltipShow: function() {
      var superShow;
      superShow = $.fn.tooltip.Constructor.prototype.show;
      return $.fn.tooltip.Constructor.prototype.show = function() {
        var _base;
        superShow.call(this);
        return typeof (_base = this.options).afterShow === "function" ? _base.afterShow(this.$tip, this.$element) : void 0;
      };
    },
    _compileTemplateForData: function() {
      var maxVal;
      if (this.collection.at(0).attributes.score != null) {
        maxVal = this.collection.max(function(model) {
          return model.attributes.score.fastest_time;
        });
        maxVal = maxVal.attributes.score.fastest_time;
      } else {
        maxVal = 9999;
      }
      this.collection.max = maxVal;
      this.collection.min = _minSpeed;
      Handlebars.registerHelper('normalizeReactionTime', function(score) {
        var max, min, normalScore;
        max = maxVal;
        min = _minSpeed;
        normalScore = (score - min) / (max - min) * 100;
        normalScore = normalScore > 95 ? 95 : normalScore;
        return Math.round(normalScore);
      });
      return this.tmpl = Handlebars.compile(tmpl);
    },
    onTooltipShow: function($tip, $el) {
      return $tip.addClass($el.prop('class'));
    }
  });
  return View;
});
