

define('game/levels/person_fill/person_vessel_view',['backbone', 'Handlebars', 'text!./person_vessel_view.hbs', 'ui_widgets/proceed'], function(Backbone, Handlebars, tmpl, proceed) {
  var View, _currentClass, _doneClass, _fillSel, _initialClass, _instructionsSel, _outputSel, _personMaskSel, _tmpl;
  _currentClass = 'current';
  _doneClass = 'finished';
  _initialClass = 'untouched';
  _instructionsSel = '.instructions';
  _fillSel = '.fill';
  _personMaskSel = '.mask';
  _outputSel = "" + _fillSel + " output";
  _tmpl = Handlebars.compile(tmpl);
  View = Backbone.View.extend({
    className: 'personVessel',
    tagName: 'li',
    events: {
      touchstart: 'onDragStart',
      touchmove: 'onDragMove',
      touchend: 'onDragStop',
      mousedown: 'onMouseDown',
      mousemove: 'onMouseMove',
      mouseup: 'onMouseUp'
    },
    initialize: function() {
      return this.listenTo(this.model, 'change:interacted', this.onChangeInteracted);
    },
    render: function() {
      this.$el.html(_tmpl(this.model.attributes));
      return this;
    },
    _match: function(event) {
      this.windowHeight = $(window).height();
      this.personHeight = this.$el.height();
      return this._follow(event);
    },
    _follow: function(event) {
      var height, percentage, touch, y;
      this.windowHeight = this.windowHeight || $(window).height();
      this.personHeight = this.personHeight || this.$el.height();
      this.$fill = this.$fill || this.$(_fillSel);
      this.$output = this.$output || this.$(_outputSel);
      if (event.pageY) {
        y = event.pageY;
      } else {
        touch = event.originalEvent.touches[0] || event.originalEvent.changedTouches[0];
        y = touch.pageY;
      }
      height = this.windowHeight - y;
      percentage = Math.round(height / this.personHeight * 100);
      percentage = this._boundValue(percentage);
      this.$fill.css({
        height: height
      });
      this.$output.text("" + percentage + "%");
      return percentage;
    },
    _setValues: function(event) {
      this.model.set({
        percentage: this._follow(event)
      });
      return this;
    },
    _setHeightFromPercentage: function(percentage) {
      this.personHeight = this.personHeight || this.$el.height();
      this.$fill = this.$fill || this.$(_fillSel);
      this.$output = this.$output || this.$(_outputSel);
      this.$fill.css({
        height: this.personHeight * percentage / 100
      });
      this.$output.text("" + percentage + "%");
      return proceed.show();
    },
    _boundValue: function(val) {
      val = val > 100 ? 100 : val;
      val = val <= 0 ? 0 : val;
      return val;
    },
    onMouseDown: function(event) {
      var _ref;
      if ((_ref = event.originalEvent) != null) {
        _ref.preventDefault();
      }
      this.isDragging = true;
      return this.onDragStart(event);
    },
    onMouseMove: function(event) {
      if (this.isDragging) {
        return this.onDragMove(event);
      }
    },
    onMouseUp: function(event) {
      this.isDragging = false;
      return this.onDragStop(event);
    },
    onDragStart: function(event) {
      this.model.set({
        interacted: true
      });
      return this._match(event);
    },
    onDragMove: function(event) {
      event.preventDefault();
      return this._follow(event);
    },
    onDragStop: function(event) {
      this._setValues(event);
      return proceed.show();
    },
    onChangeInteracted: function(model, changedAttribute) {
      return this.$el.removeClass(_initialClass);
    },
    yourTurn: function() {
      this.$el.addClass("" + _currentClass + " " + _initialClass);
      this.$(_fillSel).attr({
        tabindex: 1
      });
      return this;
    },
    done: function() {
      this.$el.addClass(_doneClass);
      this.$(_fillSel).attr({
        tabindex: -1
      });
      return this;
    },
    bumpUp: function() {
      var newVal, percent;
      percent = this.model.get('percentage');
      if (percent != null) {
        newVal = Math.round(percent / 10) + 1;
      } else {
        newVal = 1;
      }
      newVal *= 10;
      newVal = this._boundValue(newVal);
      this.model.set({
        percentage: newVal
      });
      this._setHeightFromPercentage(newVal);
      return this;
    },
    bumpDown: function() {
      var newVal, percent;
      percent = this.model.get('percentage');
      if (percent != null) {
        newVal = Math.round(percent / 10) - 1;
      } else {
        newVal = 0;
      }
      newVal *= 10;
      newVal = this._boundValue(newVal);
      this.model.set({
        percentage: newVal
      });
      this._setHeightFromPercentage(newVal);
      return this;
    },
    close: function() {
      delete this.$fill;
      delete this.personHeight;
      delete this.windowHeight;
      delete this.isDragging;
      return delete this.$output;
    }
  });
  return View;
});
