

define('ui_widgets/guest_signup',['backbone', 'text!./guest_signup.hbs', 'core'], function(Backbone, tmpl, app) {
  var Me;
  Me = Backbone.View.extend({
    className: 'guestSignup',
    events: {
      "click #ActionShowSignup": "onClickedSignUp"
    },
    initialize: function() {
      return this.listenTo(app.user, 'sync', this.onUserSync);
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    onClickedSignUp: function() {
      app.analytics.track(this.className, 'Pressed Sign Up');
      return app.trigger('session:showRegister');
    },
    onUserSync: function(userModel) {
      if (!userModel.isGuest()) {
        return app.router.navigate('dashboard', {
          trigger: true
        });
      }
    }
  });
  return Me;
});
