

define('game/levels/interest_picker/baits',['underscore', 'backbone', 'classes/model'], function(_, Backbone, Model) {
  var Bait, Export, _TYPES;
  _TYPES = {
    word: 'word',
    symbol: 'symbol'
  };
  Bait = Model.extend({
    TYPES: _TYPES,
    defaults: {
      type: _TYPES.symbol,
      value: 'Default',
      dimension: null,
      desc: null,
      isPicked: null,
      factors: {
        realistic: null,
        investigative: null,
        artistic: null,
        social: null,
        enterprising: null,
        conventional: null
      }
    }
  });
  Export = Backbone.Collection.extend({
    model: Bait,
    countPickedWords: function() {
      var words;
      words = this.filter(function(item) {
        return item.get('isPicked') && (item.get('type') === item.TYPES.word);
      });
      return words.length;
    },
    countPickedSymbols: function() {
      var symbols;
      symbols = this.filter(function(item) {
        return item.get('isPicked') && (item.get('type') === item.TYPES.symbol);
      });
      return symbols.length;
    },
    getPicked: function() {
      var picked;
      picked = this.filter(function(item) {
        return item.get('isPicked');
      });
      return _.map(picked, function(item) {
        return item.toJSON();
      });
    }
  });
  Export.TYPES = _TYPES;
  return Export;
});
