

define('entities/user_event/_events',['backbone', 'classes/model', 'core'], function(Backbone, Model, app) {
  var Export, OneEvent;
  OneEvent = Model.extend({
    defaults: function() {
      return {
        event: null,
        time: (new Date()).getTime()
      };
    },
    initialize: function() {
      return this.on('invalid', function(model, err) {
        return console.warn("event model invalid: " + err);
      });
    },
    validate: function(attrs, options) {
      if (!attrs.event) {
        return 'Requires the `event` property';
      }
      return null;
    }
  });
  Export = Backbone.Collection.extend({
    model: OneEvent
  });
  return Export;
});
