

define('game/levels/circle_proximity/main',['underscore', 'backbone', 'entities/circles', 'game/levels/_base', 'text!./instructions.hbs', './self_view', './proxy_view', 'ui_widgets/proceed'], function(_, Backbone, Circles, Level, instructions, SelfView, Proxy, proceed) {
  var View, _degToRad;
  _degToRad = function(deg) {
    return deg * Math.PI / 180;
  };
  View = Level.extend({
    className: 'circleProximity',
    initialize: function() {
      this.listenTo(this.collection, 'change:interacted', this.onChangeInteracted);
      this.listenToOnce(proceed, 'click', this._close);
      this.distanceFromSelf = 250;
      if ($(window).width() < 603) {
        return this.distanceFromSelf = 200;
      }
    },
    render: function() {
      var radius,
        _this = this;
      this.selfView = new SelfView;
      this.$el.html(this.selfView.render().el);
      this.selfView.position();
      this.$line = this.$('.line');
      radius = this.selfView.$el.width() / 2;
      this.$line.css({
        left: radius,
        top: radius
      });
      this.collection.each(function(circle) {
        circle.view = new Proxy({
          model: circle,
          $line: _this.$line,
          selfView: _this.selfView,
          track: _.bind(_this.options.runner.track, _this.options.runner)
        });
        return _this.$el.append(circle.view.render().el);
      });
      this._positionCirclesAround(this.selfView.getSelfCenter());
      return this;
    },
    _positionCirclesAround: function(center) {
      var count, deg, distance, increment;
      count = this.collection.length;
      increment = 360 / count;
      deg = increment / 2 + 270;
      distance = this.distanceFromSelf;
      return this.collection.each(function(circle) {
        circle.view.positionByCenter({
          x: Math.cos(_degToRad(deg)) * distance + center.x,
          y: Math.sin(_degToRad(deg)) * distance + center.y
        });
        return deg += increment;
      });
    },
    _close: function() {
      this.collection.each(function(circle) {
        return circle.view.remove();
      });
      proceed.hide();
      this.trigger('done');
      return this.remove();
    },
    onChangeInteracted: function() {
      if (this.checkAllInteracted(this.collection)) {
        return proceed.show();
      }
    }
  });
  return View;
});

define('game/levels/circle_proximity', ['game/levels/circle_proximity/main'], function (main) { return main; });
