  var Export, _bentoSel, _blockedClass, _countTmpl, _countdownSel, _doneMarkup, _drawerSize, _expandedClass, _margins, _menuSel, _rowCount, _rowSel, _shimmerClass, _symbolContentSel, _symbolCountSel, _symbolPicks, _tempo, _tmpl, _wordContentSel, _wordCountSel, _wordPicks;
