

define('game/levels/interest_picker/bait_view',['jquery', 'underscore', 'backbone', 'Handlebars', 'text!./bait_symbol.hbs'], function($, _, Backbone, Handlebars, tmplSymbol) {
  var Export, _animTime, _bentoBoxes, _bentoSel, _expandedClass, _menuSel, _naughtyClass, _pickAnimClass, _slidingClass, _tmplSymbol;
  _tmplSymbol = Handlebars.compile(tmplSymbol);
  _bentoSel = '.bentoBox';
  _expandedClass = 'expanded';
  _animTime = 200;
  _bentoBoxes = {
    word: '.bentoBox .words.content',
    symbol: '.bentoBox .symbols.content'
  };
  _menuSel = '.menu .content';
  _slidingClass = 'sliding';
  _pickAnimClass = 'picked';
  _naughtyClass = 'naughty';
  Export = Backbone.View.extend({
    className: 'bait',
    events: {
      'mousedown': 'onClick',
      'touchstart': 'onClick'
    },
    initialize: function() {
      return _.bindAll(this, '_picked');
    },
    render: function() {
      var _this = this;
      this._reset();
      if (this.model.attributes.type === this.model.TYPES.symbol) {
        this.$el.html(_tmplSymbol(this.model.attributes));
        this.$el.addClass(this.model.TYPES.symbol);
      } else {
        this.$el.text(this.model.attributes.value);
        this.$el.addClass(this.model.TYPES.word);
      }
      setTimeout((function() {
        return _this.$el.addClass(_slidingClass);
      }), 10);
      return this;
    },
    onClick: function(e) {
      e.preventDefault();
      if (this.model.attributes.isPicked) {
        return this.unpick().remove();
      } else {
        return this._pick();
      }
    },
    _reset: function() {
      this.delegateEvents();
      this.$el.removeClass(_slidingClass);
      this.$el.removeClass(_pickAnimClass);
      return this.$el.removeClass(_naughtyClass);
    },
    _pick: function() {
      this.timeout = setTimeout(this._picked, _animTime);
      this.$el.addClass(_pickAnimClass);
      return this.model.set({
        isPicked: true,
        record_time: (new Date()).getTime()
      }, this);
    },
    _picked: function() {
      this._reset();
      return this.$el.appendTo(_bentoBoxes[this.model.attributes.type]);
    },
    unpick: function() {
      clearTimeout(this.timeout);
      this.model.set({
        isPicked: false,
        record_time: (new Date()).getTime()
      });
      this._reset();
      return this;
    },
    sayNo: function() {
      var _this = this;
      this.$el.addClass(_naughtyClass);
      $(_bentoSel).addClass(_expandedClass);
      this.timeout = setTimeout((function() {
        return _this.$el.addClass(_slidingClass).removeClass(_naughtyClass);
      }), _animTime * 2);
      return this;
    },
    close: function() {
      clearTimeout(this.timeout);
      return this;
    }
  });
  return Export;
});
