

define('pages/error',['backbone', 'Handlebars', 'text!./error.hbs'], function(Backbone, Handlebars, tmpl) {
  var Me, _tmpl;
  _tmpl = Handlebars.compile(tmpl);
  Me = Backbone.View.extend({
    title: 'Error',
    className: 'errorPage',
    initialize: function() {
      this.error = this.options.params;
      this.error.objectString = JSON.stringify(this.error.object);
      return console.warn({
        error: this.error
      });
    },
    render: function() {
      this.$el.html(_tmpl(this.error));
      return this;
    }
  });
  return Me;
});
