

define('dashboard/career/reaction_results',['backbone', 'Handlebars', 'text!./reaction_results.hbs', 'dashboard/widgets/base', 'game/results/reaction_time'], function(Backbone, Handlebars, tmpl, Widget, ReactionResultView) {
  var View, _contentSel, _tmpl;
  _tmpl = Handlebars.compile(tmpl);
  _contentSel = '.contents';
  View = Widget.extend({
    className: 'coolTones reaction-results',
    render: function() {
      var latestResult, resultView;
      this.$el.html(_tmpl);
      latestResult = this.collection.at(this.collection.length - 1);
      latestResult.set('showTime', true);
      resultView = new ReactionResultView({
        model: latestResult
      });
      this.$(_contentSel).html(resultView.render().el);
      return this;
    }
  });
  View.dependsOn = 'entities/results/results';
  View.fetchOptions = {
    data: {
      type: 'ReactionTimeResult'
    }
  };
  return View;
});
