

define('entities/my/career',['classes/model', 'core'], function(Model, app) {
  var Export;
  Export = Model.extend({
    url: "" + app.cfg.apiServer + "/api/v1/users/-/recommendations/career",
    initialize: function() {
      this.on('sync', this.onSync);
      return this.on('error', this.onErr);
    },
    onSync: function(model) {},
    onErr: function() {
      return console.error("Trouble getting model data");
    }
  });
  return Export;
});
