

define('game/levels/proximity_takes_turns/self_view',['game/levels/circle_proximity/self_view', 'text!./self_view.hbs'], function(CircleProximitySelfView, tmpl) {
  var View, _userIconSel;
  _userIconSel = '.user';
  View = CircleProximitySelfView.extend({
    tmpl: tmpl,
    getSelfCenter: function() {
      var $userIcon;
      if (this._center) {
        return this._center;
      }
      $userIcon = this.$(_userIconSel);
      this._center = {};
      this._center.x = $(window).width() / 2;
      this._center.y = $userIcon.position().top;
      return this._center;
    },
    getSelfRadius: function() {
      this._rad = this._rad || this.$el.height() / 2;
      return this._rad;
    }
  });
  return View;
});
