

define('utils/numbers',[], function() {
  var numbers;
  numbers = {
    flipCoin: function() {
      return !!Math.floor(Math.random() * 2);
    },
    casino: function(odds) {
      odds = odds || 0.5;
      return !!(odds > Math.random());
    },
    pickOneAnyOne: function(array) {
      return array[Math.floor(Math.random() * array.length)];
    }
  };
  return numbers;
});
