

define('core/analytics/google',[], function() {
  var GoogleAnalytics;
  GoogleAnalytics = function(key, isDev) {
    (function(i, s, o, g, r, a, m) {
      i["GoogleAnalyticsObject"] = r;
      i[r] = i[r] || function() {
        return (i[r].q = i[r].q || []).push(arguments);
      };
      i[r].l = 1 * new Date();
      a = s.createElement(o);
      m = s.getElementsByTagName(o)[0];
      a.async = 1;
      a.src = g;
      return m.parentNode.insertBefore(a, m);
    })(window, document, "script", "//www.google-analytics.com/analytics.js", "ga");
    if (isDev) {
      return ga("create", key, "tide-dev.herokuapp.com");
    } else {
      return ga("create", key);
    }
  };
  GoogleAnalytics.prototype = {
    trackPage: function(pageName) {
      return ga('send', 'pageview', {
        location: document.URL,
        page: "/" + pageName
      });
    },
    trackEvent: function(category, action, label, val) {
      return ga('send', 'event', {
        eventCategory: category,
        eventAction: action,
        eventLabel: label,
        eventValue: val
      });
    },
    setFirstVisit: function() {
      var d, date, m, y;
      date = new Date();
      y = date.getFullYear();
      m = date.getMonth() + 1;
      d = date.getDate();
      if (m.length === 1) {
        m = "0" + m;
      }
      if (d.length === 1) {
        d = "0" + d;
      }
      return ga('set', 'dimension1', y + m + d);
    },
    setGamesPlayed: function(gameCount) {
      return ga('set', 'metric1', gameCount);
    }
  };
  return GoogleAnalytics;
});
