

define('pages/social_results',['backbone', 'Handlebars', 'core', 'text!./social_results.hbs', 'text!./social_results-not_enough.hbs', 'entities/friend_survey', 'charts/polar_area'], function(Backbone, Handlebars, app, tmpl, tmplNotEnough, FriendResults, Chart) {
  var Me, _contentSel, _selfContentSel, _socialContentSel, _tmpl, _tmplNotEnough;
  _tmpl = Handlebars.compile(tmpl);
  _tmplNotEnough = Handlebars.compile(tmplNotEnough);
  _contentSel = '.contents';
  _selfContentSel = '.contentSelf';
  _socialContentSel = '.contentSocial';
  Me = Backbone.View.extend({
    title: 'Social Survey Results',
    className: 'socialResultsPage',
    initialize: function() {
      if (!this.options.params.game_id) {
        console.error("Need game_id");
      }
      this.model = new FriendResults({
        game_id: this.options.params.game_id
      });
      this.listenTo(this.model, 'sync', this.onSync);
      this.listenTo(this.model, 'error', this.onErr);
      return this.model.fetch();
    },
    render: function() {
      this.$el.html(_tmpl(this.options.params));
      return this;
    },
    renderContent: function() {},
    onSync: function() {
      this.chartSelf = new Chart({
        data: {
          name: "Self-Reported",
          chartValues: this.model.attributes.my_results,
          width: 350,
          height: 300,
          options: {
            animation: false
          }
        }
      });
      this.chartSocial = new Chart({
        data: {
          name: "Social Personality",
          chartValues: this.model.attributes.others_results,
          width: 350,
          height: 300
        }
      });
      this.$(_contentSel).remove();
      this.$(_selfContentSel).append(this.chartSelf.render().el);
      return this.$(_socialContentSel).append(this.chartSocial.render().el);
    },
    onErr: function() {
      return this.$el.html(_tmplNotEnough({
        game_id: this.options.params.game_id,
        origin: window.location.protocol + '//' + window.location.hostname
      }));
    }
  });
  return Me;
});
