

define('game/results/emotions',['backbone', 'Handlebars', 'game/results/base', 'text!./emotions.hbs', 'markdown'], function(Backbone, Handlebars, ResultView, tmpl, markdown) {
  var View, _emoticonSel, _renderDelay;
  _emoticonSel = '.emoticon';
  _renderDelay = 200;
  View = ResultView.extend({
    className: 'emotions',
    tmpl: Handlebars.compile(tmpl),
    render: function() {
      var attrs, score,
        _this = this;
      score = _.clone(this.model.attributes.score);
      if (score.display_emotion_title) {
        score.display_emotion_title = markdown.toHTML(score.display_emotion_title);
      }
      if (score.display_emotion_description) {
        score.display_emotion_description = markdown.toHTML(score.display_emotion_description);
      }
      attrs = this.model.toJSON();
      attrs.score = score;
      this.$el.html(this.tmpl(attrs));
      setTimeout((function() {
        return _this.$(_emoticonSel).addClass(_this.model.attributes.score.display_emotion_name);
      }), _renderDelay);
      return this;
    }
  });
  return View;
});
