

define('entities/progress/_base',['underscore', 'backbone'], function(_, Backbone) {
  var Export, STATES;
  STATES = {
    pending: 'pending',
    done: 'done',
    error: 'error'
  };
  Export = Backbone.Model.extend({
    maxPolls: 10,
    pollWait: 500,
    initialize: function(options) {
      return _.bindAll(this, 'onGotStarted', 'onPromiseFail', '_poll');
    },
    fetch: function() {
      var promise, url;
      url = _.result(this, 'url');
      if (!url) {
        console.error("Need a url");
      }
      promise = $.get(url);
      promise.done(this.onGotStarted);
      promise.fail(this.onPromiseFail);
      return this;
    },
    onGotStarted: function(resp, httpStatus, xhr) {
      if (resp.status.link == null) {
        console.error("response doesn't include a link");
      }
      this.polls = 0;
      return this._poll(resp);
    },
    onPromiseFail: function(jqXHR, textStatus, errorThrown) {
      return this.trigger('error', this, textStatus, {
        jqXHR: jqXHR,
        textStatus: textStatus,
        errorThrown: errorThrown
      });
    },
    _poll: function(resp) {
      var promise,
        _this = this;
      switch (resp.status.state) {
        case STATES.pending:
          if (this.polls >= this.maxPolls) {
            return this.trigger('error', this, 'Timed out polling for progress');
          } else {
            this.polls++;
            promise = $.get(resp.status.link);
            promise.done(function(resp) {
              return setTimeout((function() {
                return _this._poll(resp);
              }), _this.pollWait);
            });
            return promise.fail(this.onPromiseFail);
          }
          break;
        case STATES.done:
          return this.trigger('sync', this, "Progress completed");
        case STATES.error:
          return this.trigger('error', this, "Error polling for progress");
        default:
          return console.warn("unknown poll state");
      }
    }
  });
  return Export;
});
