

define('entities/preferences/training-descriptions',['classes/collection'], function(Collection) {
  var Export;
  Export = Collection.extend({
    url: function() {
      return "" + window.apiServerUrl + "/api/v1/preferences/training-preference/description";
    },
    setValues: function(values) {
      var key, model, val;
      if (!values) {
        return this;
      }
      for (key in values) {
        val = values[key];
        model = this.find(function(model) {
          return model.attributes.name === key;
        });
        if (model != null) {
          model.set({
            value: val
          });
        }
      }
      return this;
    },
    getValues: function() {
      return console.warn('not implemented');
    }
  });
  return Export;
});
