

define('charts/doughnut',['backbone', 'Handlebars', './chart', 'text!./chart_with_swatches.hbs', 'chart'], function(Backbone, Handlebars, ChartModel, tmpl, ChartLib) {
  var View, _canvasSel;
  _canvasSel = 'canvas';
  View = Backbone.View.extend({
    tmpl: Handlebars.compile(tmpl),
    className: 'chart doughnut',
    initialize: function() {
      if (!this.options.data) {
        throw new Error('Need options.data');
      }
      return this.model = new ChartModel(this.options.data, {
        parse: true
      });
    },
    render: function() {
      this.$el.html(this.tmpl(this.model.attributes));
      this._renderChart(this.model.attributes.chartValues);
      return this;
    },
    _renderChart: function(chartData) {
      var $canvas, barChart, ctx, options;
      $canvas = this.$(_canvasSel);
      if (!$canvas) {
        return;
      }
      options = _.extend(this.model.attributes.options, {
        percentageInnerCutout: 33
      });
      ctx = $canvas[0].getContext("2d");
      return barChart = new Chart(ctx).Doughnut(chartData, options);
    }
  });
  return View;
});
