

define('dashboard/personality/holland6',['backbone', 'dashboard/widgets/base', 'composite_views/perch', 'charts/doughnut'], function(Backbone, Widget, perch, DoughnutChart) {
  var View, _chartName, _widgetSel;
  _widgetSel = '.widget';
  _chartName = 'Interests';
  View = Widget.extend({
    events: {
      'click .widget': 'onClick'
    },
    className: 'chart-holland6',
    render: function() {
      this.$el.html(this.tmplBase({
        title: _chartName,
        className: 'pressable'
      }));
      this.chart = new DoughnutChart({
        data: {
          name: _chartName,
          chartValues: this.model.attributes.holland6_score,
          options: {
            animation: false
          }
        }
      });
      this.$(_widgetSel).append(this.chart.render().el);
      return this;
    },
    onClick: function() {
      return perch.show({
        title: _chartName,
        large: true,
        btn1Text: null,
        content: new DoughnutChart({
          data: {
            name: _chartName,
            chartValues: this.model.attributes.holland6_score,
            width: 450,
            height: 300
          }
        })
      });
    }
  });
  View.dependsOn = 'entities/my/personality';
  return View;
});
