

define('game/levels/_base/main',['jquery', 'underscore', 'backbone', 'entities/user_event/_event_bundle', 'composite_views/perch', 'ui_widgets/proceed'], function($, _, Backbone, EventBundle, perch, proceed) {
  var Export, _me;
  _me = 'game/levels/_base';
  Export = Backbone.View.extend({
    initialize: function() {
      if (!((this.options.assessment != null) && (this.options.stageNo != null) && (this.options.stageDef != null))) {
        throw new Error("Need assessment and stageNo and stageDef");
      }
      this.event = new EventBundle({
        event_type: this.options.stageDef,
        stage: this.options.stageNo,
        game_id: this.options.assessment.get('id')
      });
      if (this.model.attributes.data != null) {
        if (this.CollectionClass) {
          this.collection = new this.CollectionClass(this.model.attributes.data);
        } else {
          this.collection = new Backbone.Collection(this.model.attributes.data);
        }
      }
      if (typeof this.start === "function") {
        return this.start();
      } else {
        return this.track(EventBundle.EVENTS.start);
      }
    },
    _fillHeight: function() {
      return this.$el.css({
        height: this.getAvailHeight()
      });
    },
    clearInteracted: function(collection) {
      return collection.each(function(item) {
        return item.set('interacted', null);
      });
    },
    checkAllInteracted: function(collection) {
      var interacted;
      interacted = collection.filter(function(item) {
        return item.get('interacted');
      });
      if (interacted.length === collection.length) {
        return true;
      }
    },
    readyToProceed: function() {
      if (this.alreadyReady) {
        return;
      }
      this.alreadyReady = true;
      proceed.show();
      this.listenToOnce(proceed, 'click', this.endLevel);
      return this.track(EventBundle.EVENTS.readyToProceed);
    },
    notReadyToProceed: function() {
      if (!this.alreadyReady) {
        return;
      }
      this.alreadyReady = false;
      proceed.hide();
      this.stopListening(proceed);
      return this.track(EventBundle.EVENTS.notReadyToProceed);
    },
    endLevel: function() {
      proceed.hide();
      this.track(EventBundle.EVENTS.summary, this.summaryData);
      this.track(EventBundle.EVENTS.end);
      this.event.save();
      this.trigger('done', this.summaryData);
      if (typeof this.close === "function") {
        this.close();
      }
      this.remove();
      return this.options.assessment.nextStage();
    },
    track: function(eventName, eventData) {
      var baseData;
      baseData = {
        event: eventName
      };
      return this.event.record(_.extend(baseData, eventData));
    },
    getAvailHeight: function() {
      var availHeight;
      availHeight = $(window).height() - this.$el.offset().top;
      if (this.heightAdjustment) {
        availHeight -= this.heightAdjustment;
      }
      return availHeight;
    },
    fillHeight: function() {
      var debounced;
      this._fillHeight();
      debounced = _.debounce(this._fillHeight, 100);
      this.debouncedFill = _.bind(debounced, this);
      return $(window).on('resize', this.debouncedFill);
    },
    remove: function() {
      if (typeof this.close === "function") {
        this.close();
      }
      $(window).off('resize', this.debouncedFill);
      proceed.hide();
      this.$el.remove();
      this.stopListening();
      return this;
    }
  });
  Export.EVENTS = EventBundle.EVENTS;
  return Export;
});

define('game/levels/_base', ['game/levels/_base/main'], function (main) { return main; });
