

define('dashboard/emotions/highest_emotion',['backbone', 'Handlebars', 'dashboard/widgets/base', 'game/results/emotions', 'text!./highest_emotion.hbs'], function(Backbone, Handlebars, Widget, EmotionsResultView, tmpl) {
  var View, _widgetSel;
  _widgetSel = '.widget';
  View = Widget.extend({
    className: 'doubleWide tall highestEmotion',
    render: function() {
      var latestResult, view;
      this.$el.html(tmpl);
      latestResult = this.collection.at(this.collection.length - 1);
      view = new EmotionsResultView({
        model: latestResult
      });
      this.$(_widgetSel).append(view.render().el);
      return this;
    }
  });
  View.dependsOn = 'entities/results/results';
  View.fetchOptions = {
    data: {
      type: 'EmoResult'
    }
  };
  return View;
});
