

define('dashboard/career/reaction_history',['backbone', 'Handlebars', 'text!./reaction_history.hbs', 'dashboard/widgets/base', 'game/results/reaction_time_history'], function(Backbone, Handlebars, tmpl, Widget, HistoryView) {
  var View, _widgetSel;
  _widgetSel = '.widget';
  View = Widget.extend({
    className: 'coolTones doubleWide reaction-history',
    render: function() {
      var historyView;
      this.$el.html(tmpl);
      historyView = new HistoryView({
        collection: this.collection
      });
      this.$(_widgetSel).append(historyView.render().el);
      historyView.renderChart();
      return this;
    }
  });
  View.dependsOn = 'entities/results/results';
  View.fetchOptions = {
    data: {
      type: 'ReactionTimeResult'
    }
  };
  return View;
});
