

define('dashboard/emotions/historical_highest',['backbone', 'Handlebars', 'dashboard/widgets/base', 'game/results/emotions_history'], function(Backbone, Handlebars, Widget, HistoryView) {
  var View, _widgetSel;
  _widgetSel = '.widget';
  View = Widget.extend({
    className: 'doubleWide historicalHighestEmotion',
    render: function() {
      var view;
      this.$el.html(this.tmplBase({
        title: 'Your Emotional Timeline'
      }));
      view = new HistoryView({
        collection: this.collection
      });
      this.$(_widgetSel).append(view.render().el);
      view.renderChart();
      return this;
    }
  });
  View.dependsOn = 'entities/results/results';
  View.fetchOptions = {
    data: {
      type: 'EmoResult'
    }
  };
  return View;
});
