

define('entities/results_calculator',['jquery', 'backbone'], function($, Backbone) {
  var Export, _maxPollCount, _maxTryCount, _me;
  _me = 'entities/results_calculator';
  _maxTryCount = 2;
  _maxPollCount = 15;
  Export = Backbone.Model.extend({
    STATES: {
      pending: 'pending',
      error: 'error',
      done: 'done'
    },
    urlRoot: function() {
      return "" + window.apiServerUrl + "/api/v1/users/-/games/" + (this.get('game_id')) + "/results";
    },
    initialize: function() {
      this.tries = 0;
      this.on('sync', this.onSync);
      return this.tryTryAgain();
    },
    tryTryAgain: function() {
      this.polls = 0;
      return this.fetch();
    },
    pollForProgress: function(url) {
      var promise,
        _this = this;
      promise = $.ajax({
        type: 'GET',
        url: url
      });
      promise.done(function(data, textStatus, jqXHR) {
        if (jqXHR.status !== 200) {
          console.log("Unexpected StatusCode: " + jqXHR.status);
        }
        switch (data.status.state) {
          case _this.STATES.pending:
            _this.polls += 1;
            if (_this.polls >= _maxPollCount) {
              return _this.trigger('error', _this, 'Timed out waiting for results.');
            } else {
              return setTimeout(function() {
                return _this.pollForProgress(data.status.link);
              }, 500);
            }
            break;
          case _this.STATES.done:
            return _this.set('status', data.status);
          case _this.STATES.error:
            return _this._handleServerError(data);
          default:
            return _this.trigger('error', _this, "Unexpected status " + data.status.state);
        }
      });
      return promise.fail(function(jqXHR, textStatus, errorThrown) {
        return _this.trigger('error', _this, textStatus, {
          jqXHR: jqXHR,
          textStatus: textStatus,
          errorThrown: errorThrown
        });
      });
    },
    _handleServerError: function(data) {
      this.tries++;
      console.log("Error on try " + this.tries);
      if (this.tries >= _maxTryCount) {
        return this.trigger('error', this, data.status.message);
      } else {
        return this.tryTryAgain();
      }
    },
    onSync: function(model) {
      return this.pollForProgress(model.attributes.status.link);
    }
  });
  return Export;
});
