

define('composite_views/perch-psst',['jquery', 'backbone', 'Handlebars', "./perch"], function($, Backbone, Handlebars, perch) {
  var Me, _contentSel, _iconTmpl, _me;
  _me = 'composite_views/perch-psst';
  _contentSel = '#PerchBody';
  _iconTmpl = Handlebars.compile('\
    <p class="row">\
      <i class="icon-2x {{icon}}"></i>\
    </p>\
  ');
  Me = perch.Klass.extend({
    _showOptionsObject: function(options) {
      var dropStyle;
      options.btn1Text = null;
      options.content = '';
      if (options.icon) {
        options.content += _iconTmpl(options);
      }
      if (options.msg) {
        options.content += "<p class='large'>" + options.msg + "</p>";
      }
      options.content = "<div class='highlight'>" + options.content + "</div>";
      this.model.set(options);
      this._bindDomEvents();
      dropStyle = options.mustUseButton ? 'static' : true;
      return this.$el.modal({
        backdrop: dropStyle
      });
    }
  });
  return new Me();
});
