

define('game/levels/snoozer/summary',['underscore', 'classes/model', 'utils/detect'], function(_, Model, detect) {
  var Export;
  Export = Model.extend({
    defaults: {
      total: null,
      decoys: null,
      correct: 0,
      incorrect: 0,
      missed: 0,
      reaction_times: []
    },
    initialize: function() {
      return this.set('is_touch', detect.isTouch());
    },
    addTime: function(reactionTime) {
      var rt;
      rt = this.get('reaction_times');
      rt.push(reactionTime);
      return this.set({
        reaction_times: rt
      });
    },
    calculate: function() {
      var sum, times;
      times = this.get('reaction_times');
      if (!times.length) {
        return;
      }
      this.set({
        fastest_time: _.min(times)
      });
      this.set({
        slowest_time: _.max(times)
      });
      sum = _.reduce(times, function(memo, num) {
        return memo + num;
      });
      this.set({
        average_time: sum / times.length
      });
      return this;
    }
  });
  return Export;
});
