

define('forms/user/profile',['jquery', 'underscore', 'backbone', 'Handlebars', 'text!./profile.hbs', 'ui_widgets/formation', './profile-fields', 'ui_widgets/hold_please', 'ui_widgets/psst', 'composite_views/perch'], function($, _, Backbone, Handlebars, tmpl, Formation, profileFields, holdPlease, psst, perch) {
  var ProfileDialog, _errorHolderMarkup, _errorHolderSel, _me, _submitBtnSel, _tmpl;
  _me = 'forms/profile/user_profile';
  _submitBtnSel = '[type=submit]';
  _errorHolderSel = '.formation .buttonArea';
  _errorHolderMarkup = "<div id='ProfileErrorHolder'></div>";
  _tmpl = Handlebars.compile(tmpl);
  ProfileDialog = Backbone.View.extend({
    className: 'userProfile',
    events: {
      'submit form': 'onSubmit',
      'input change': 'onInputChange'
    },
    initialize: function() {
      this.listenTo(this.model, 'error', this.onError);
      this.listenTo(this.model, 'invalid', this.onInvalid);
      return this.listenTo(this.model, 'sync', this.onSync);
    },
    render: function() {
      this.$el.html(_tmpl(this.model.attributes));
      this.form = new Formation({
        data: profileFields,
        values: this.model.attributes,
        submitBtn: {
          className: 'btn-large btn-block btn-inverse'
        }
      });
      this.$el.append(this.form.render().el);
      return this;
    },
    _showErr: function(msg) {
      psst.hide();
      psst({
        sel: _errorHolderSel,
        msg: msg,
        type: 'error'
      });
      return holdPlease.hide(this.$(_submitBtnSel));
    },
    onInputChange: function() {
      return psst.hide();
    },
    onSubmit: function(e) {
      var formData;
      e.preventDefault();
      psst.hide();
      holdPlease.show(this.$(_submitBtnSel));
      formData = this.form.getVals();
      return this.model.save(formData, {
        wait: true,
        profile: true
      });
    },
    onSync: function() {
      return perch.hide();
    },
    onError: function(model, xhr) {
      var msg;
      if (xhr.status === 0) {
        msg = 'Unknown Server Error';
      } else {
        msg = "" + xhr.status + ": " + xhr.statusText;
      }
      return this._showErr(msg);
    },
    onInvalid: function(model, msg) {
      return this._showErr(msg);
    }
  });
  return ProfileDialog;
});
