

define('entities/circles',['underscore', 'backbone', 'classes/model'], function(_, Backbone, Model) {
  var Circle, Collection, _me;
  _me = 'game/levels/circle_proximity/circles';
  Circle = Model.extend({
    maxSize: 4,
    baseSizePx: 75,
    sizeToScale: [0.4, 0.7, 1.0, 1.3, 1.6],
    sizeToClass: ['size1', 'size2', 'size3', 'size4', 'size5'],
    defaults: {
      selfProximityPx: null,
      selfOverlapPx: null,
      selfOverlapRatio: null,
      userChangedSize: false,
      userChangedPos: false,
      width: null,
      top: 0,
      left: 0,
      pos: {
        x: 0,
        y: 0
      }
    },
    initialize: function() {
      this._calcAbbreviation();
      this._calcImageUrl();
      this.set('size', 2);
      this._setWidthBySize(this.get('size'));
      this.on('change:size', this.onChangeSize);
      this.on('change:pos', this.onChangePos);
      return this.on('change:percentage', this.onChangePercentage);
    },
    toJSON: function(options) {
      var attrs;
      attrs = _.clone(this.attributes);
      delete attrs.interacted;
      delete attrs.focus;
      delete attrs.abbreviation;
      delete attrs.userChangedPos;
      delete attrs.userChangedSize;
      return attrs;
    },
    _calcAbbreviation: function() {
      var abbr, t1, t2;
      t1 = this.get('trait1');
      t2 = this.get('trait2');
      if (!(t1 && t2)) {
        return;
      }
      t1 = t1.charAt(0);
      t2 = t2.charAt(0);
      abbr = "" + t1 + "<em>/</em>" + t2;
      return this.set('abbreviation', abbr);
    },
    _calcImageUrl: function() {
      var imgName;
      imgName = this.get('image_id');
      if (!imgName) {
        return;
      }
      return this.set('image_url', "/images/game/emotions/" + imgName + ".png");
    },
    _setWidthBySize: function(size) {
      return this.set({
        width: this.sizeToScale[size] * this.baseSizePx
      });
    },
    _setSizeByPercent: function(percent) {
      var size, step;
      step = Math.round((percent / 20) + .5);
      size = step - 1;
      return this.set({
        size: size
      });
    },
    onChangeSize: function(model, sz) {
      this.set({
        userChangedSize: true,
        interacted: true
      });
      return this._setWidthBySize(sz);
    },
    onChangePos: function(model, pos) {
      return this.set({
        userChangedPos: true,
        interacted: true
      });
    },
    onChangePercentage: function(model, percent) {
      return this._setSizeByPercent(percent);
    },
    getScale: function() {
      return this.sizeToScale[this.get('size')];
    }
  });
  Collection = Backbone.Collection.extend({
    model: Circle
  });
  return Collection;
});
