

define('ui_widgets/formation/select_by_icon',['jquery', 'backbone', 'Handlebars', 'text!./select_by_icon.hbs'], function($, Backbone, Handlebars, tmpl) {
  var Me, _btnHolderSel, _me, _selectedClass, _tmpl;
  _me = 'ui_widgets/formation/select_by_icon';
  _tmpl = Handlebars.compile(tmpl);
  _selectedClass = 'active';
  _btnHolderSel = '.purdyBts';
  Me = Backbone.View.extend({
    className: 'field selectByIcon',
    events: {
      'click .btn': 'onBtnClick',
      'change select': 'onSelectChange'
    },
    initialize: function() {
      return this.$el.addClass(this.model.attributes.string_id);
    },
    render: function() {
      this.$el.html(_tmpl(this.model.attributes));
      this.$(_btnHolderSel).css({
        'display': 'inline-block'
      });
      this.$('select').hide();
      return this;
    },
    _syncSelectByBtn: function() {
      var selectedVals;
      selectedVals = [];
      this.$('button').each(function() {
        if ($(this).hasClass(_selectedClass)) {
          return selectedVals.push($(this).val());
        }
      });
      this.$('select').val(selectedVals);
      this.model.set('value', this.$('select').val());
      return this.$('select').trigger('change');
    },
    _syncBtnBySelect: function() {
      var selectedVal;
      selectedVal = this.$('select').val();
      return this.$('button').each(function() {
        $(this).removeClass(_selectedClass);
        if (_.isArray(selectedVal)) {
          if (_.contains(selectedVal, $(this).val())) {
            return $(this).addClass(_selectedClass);
          }
        } else {
          if ($(this).val() === selectedVal) {
            return $(this).addClass(_selectedClass);
          }
        }
      });
    },
    onBtnClick: function(e) {
      var $targ, clickedVal;
      $targ = $(e.currentTarget);
      clickedVal = $targ.val();
      $targ.toggleClass(_selectedClass);
      if ($targ.hasClass(_selectedClass)) {
        if (!this.model.attributes.multiselect) {
          $targ.siblings("." + _selectedClass).removeClass(_selectedClass);
        }
      }
      return this._syncSelectByBtn();
    },
    onSelectChange: function(e) {
      this._syncBtnBySelect();
      return this.trigger('change', {
        model: this.model,
        value: this.$('select').val()
      });
    }
  });
  return Me;
});
