

define('entities/connection',['classes/model', 'entities/progress/connection'], function(Model, ConnectionProgress) {
  var Export, PROVIDERNAMES, _linkRoot;
  _linkRoot = "" + window.apiServerUrl + "/auth/new?";
  PROVIDERNAMES = {
    'fitbit': 'Fitbit',
    'nike': 'Nike',
    'facebook': 'Facebook',
    'twitter': 'Twitter',
    'instagram': 'Instagram'
  };
  Export = Model.extend({
    defaults: function() {
      return {
        redirect_uri: document.URL,
        link: 'javascript:void(0);'
      };
    },
    initialize: function() {
      this.progress = new ConnectionProgress({
        provider: this.attributes.provider
      });
      this.listenTo(this.progress, 'error', this.onProgressError);
      this.listenTo(this.progress, 'sync', this.onProgressSync);
      this._setActivationLink();
      return this.set('display_name', PROVIDERNAMES[this.attributes.provider] || this.attributes.provider);
    },
    _setActivationLink: function() {
      var params;
      params = this.pick('provider', 'redirect_uri', 'user_id');
      return this.set({
        link: "" + _linkRoot + ($.param(params))
      });
    },
    onProgressError: function(progressModel, msg) {
      console.warn("Error getting progress on the connection: " + msg);
      this.unset('isPolling');
      return this.set({
        msg: 'Error'
      });
    },
    onProgressSync: function(progressModel) {
      this.unset('msg');
      return this.unset('isPolling');
    },
    syncConnection: function() {
      this.set({
        msg: 'Syncing...'
      });
      this.set({
        isPolling: true
      });
      return this.progress.fetch();
    }
  });
  return Export;
});
