

define('entities/my/games',['jquery', 'classes/model'], function($, Model) {
  var Export;
  Export = Model.extend({
    urlRoot: "" + window.apiServerUrl + "/api/v1/users/-/games",
    initialize: function() {
      this._levelsSeen = [];
      return this;
    },
    toJSON: function(options) {
      return _.pick(this.attributes, 'stage_completed', 'def_id');
    },
    getLatestWithProfile: function() {
      var deferred,
        _this = this;
      deferred = $.Deferred();
      this.fetch({
        url: "" + (this.url()) + "/latest_with_profile"
      }).done(function(data, textStatus, jqXHR) {
        console.log('Got the latest game');
        return deferred.resolve();
      }).fail(function(jqXHR, textStatus, errorThrown) {
        return deferred.reject();
      });
      return deferred.promise();
    },
    create: function(gameDefinitionId) {
      if (gameDefinitionId) {
        this.save({
          def_id: gameDefinitionId
        });
      } else {
        this.save();
      }
      return this;
    },
    nextStage: function() {
      var i;
      i = this.get('stage_completed');
      return this.save({
        stage_completed: i + 1
      });
    },
    setLevelSeen: function(levelStringId) {
      this._levelsSeen.push(levelStringId);
      this._levelsSeen = _.uniq(this._levelsSeen);
      return this;
    },
    isFirstTimeSeeingLevel: function(levelStringId) {
      if (!levelStringId) {
        return null;
      }
      if (_.contains(this._levelsSeen, levelStringId)) {
        return false;
      }
      return true;
    }
  });
  return Export;
});
