

define('core/router',['jquery', 'backbone'], function($, Backbone) {
  var MainRouter, _me, _welcomePageUrlPrefix;
  _me = 'routers/main_router';
  _welcomePageUrlPrefix = '/welcome/';
  MainRouter = Backbone.Router.extend({
    routes: {
      '': 'showHome',
      home: 'showHome',
      about: 'showAbout',
      team: 'showTeam',
      getStarted: 'getStarted',
      startGame: 'getStarted',
      demographics: 'showDemographics',
      'game/:def_id': 'createGame',
      game: 'createDefaultGame',
      'gameForUser/:token': 'createGameForUser',
      'gameResults/:id': 'showGameResults',
      dashboard: 'showDashboard',
      'dashboard-mood': 'showDashMood',
      'dashboard-personality': 'showDashPersonality',
      'dashboard-productivity': 'showDashProductivity',
      'referrer/:refId': 'recordReferrer',
      'preferences-training': 'showTrainingPreferences',
      'friendHelper/:uid/:gid': 'showFriendHelper',
      'friendResults/:gid': 'showFriendResults'
    },
    initialize: function(appCoreSingleton) {
      return this.app = appCoreSingleton;
    },
    showHome: function() {
      if (this.app.user.attributes.referred_by != null) {
        return window.location.href = _welcomePageUrlPrefix + this.app.user.attributes.referred_by;
      } else {
        return this.app.view.asSite('pages/home');
      }
    },
    getStarted: function() {
      if (this.app.user.attributes.personality != null) {
        return this.navigate('dashboard', {
          trigger: true,
          replace: true
        });
      } else {
        return this.createDefaultGame();
      }
    },
    showAbout: function() {
      return this.app.view.asSite('pages/about');
    },
    showTeam: function() {
      return this.app.view.asSite('pages/team');
    },
    showDemographics: function() {
      return this.app.view.asGame('pages/demographics');
    },
    createDefaultGame: function() {
      return this.createGame('baseline');
    },
    createGame: function(def_id) {
      return this.app.view.asGame('pages/play_game', {
        def_id: def_id
      });
    },
    createGameForUser: function(token) {
      this.listenToOnce(this.app.user, 'sync', function() {
        return this.createGame('baseline');
      });
      this.listenToOnce(this.app.user, 'error', function(model, xhr) {
        return this.showError(xhr.responseJSON.status.message, model);
      });
      return this.app.user.reset(token).fetch();
    },
    showGameResults: function(id) {
      return this.app.view.asGame('pages/game_results', {
        game_id: id
      });
    },
    showDashboard: function() {
      return this.app.view.asDash('pages/dashboard/summary');
    },
    showDashProductivity: function() {
      return this.app.view.asDash('pages/dashboard/productivity');
    },
    showDashMood: function() {
      return this.app.view.asDash('pages/dashboard/mood');
    },
    showDashPersonality: function() {
      return this.app.view.asDash('pages/dashboard/personality');
    },
    showFriendHelper: function(uid, gid) {
      return this.app.view.asGame('pages/friend_survey', {
        user_id: uid,
        game_id: gid
      });
    },
    showFriendResults: function(gid) {
      return this.app.view.asGame('pages/social_results', {
        game_id: gid
      });
    },
    recordReferrer: function(refId) {
      this.app.analytics.track('Referral', refId);
      this.app.user.set({
        referred_by: refId
      });
      this.showDemographics();
      return this.navigate('startGame', {
        replace: true
      });
    },
    showTrainingPreferences: function() {
      this.navigate('dashboard');
      return this.app.trigger('action:showPersonalizations');
    },
    showError: function(message, object) {
      return this.app.view.asSite('pages/error', {
        message: message,
        object: object
      });
    }
  });
  return MainRouter;
});
