

define('charts/pie',['backbone', 'Handlebars', './chart', 'text!./chart_with_swatches.hbs', 'chart'], function(Backbone, Handlebars, ChartModel, tmpl, ChartLib) {
  var View, _canvasSel;
  _canvasSel = 'canvas';
  View = Backbone.View.extend({
    tmpl: Handlebars.compile(tmpl),
    className: 'chart pie',
    initialize: function() {
      return this.model = new ChartModel(this.options.data, {
        parse: true
      });
    },
    render: function() {
      this.$el.html(this.tmpl(this.model.attributes));
      this._renderChart(this.model.attributes.chartValues);
      return this;
    },
    _renderChart: function(chartData) {
      var $canvas, barChart, ctx, max, options;
      $canvas = this.$(_canvasSel);
      if (!$canvas) {
        return;
      }
      max = _.max(chartData, function(d) {
        return d.value;
      });
      max = Math.ceil(max.value);
      options = _.extend(this.model.attributes.options, {
        scaleShowLine: false,
        scaleShowLabels: false,
        scaleOverride: true,
        scaleSteps: max,
        scaleStepWidth: 1,
        scaleStartValue: 0
      });
      ctx = $canvas[0].getContext("2d");
      return barChart = new Chart(ctx).Pie(chartData, options);
    }
  });
  return View;
});
