

define('connections/connection_list',['backbone', 'text!./connection_list.hbs', 'entities/my/connections', './connection_view'], function(Backbone, tmpl, MyConnections, ConnectionView) {
  var View, _containerSel;
  _containerSel = '.connections';
  View = Backbone.View.extend({
    className: 'connectionList',
    events: {
      'click #ActionActiviateFitbit': 'onClickFitbit'
    },
    initialize: function() {
      this.collection = new MyConnections({
        app: this.options.app
      });
      this.listenTo(this.collection, 'sync', this.onSync);
      return this.collection.fetch();
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    _renderList: function() {
      return this.collection.each(function(model) {
        model.view = new ConnectionView({
          model: model
        });
        return this.$(_containerSel).append(model.view.render().el);
      });
    },
    onSync: function() {
      return this._renderList();
    }
  });
  return View;
});
