

define('game/results/emotions_history',['backbone', 'Handlebars', 'game/results/base', 'text!./emotions_history.hbs', 'composite_views/perch', 'game/results/emotions'], function(Backbone, Handlebars, ResultView, tmpl, perch, EmotionsResultsView) {
  var TIMECLASSES, TIMESTRINGS, View, _dateToTimeFeatureByArray, _emoticonSel, _holderSel, _me, _scrollFixDelay, _tmpl;
  _holderSel = '.chartHolder';
  _scrollFixDelay = 200;
  _emoticonSel = '.emoticon';
  _me = 'game/results/emotions_history';
  _tmpl = Handlebars.compile(tmpl);
  TIMECLASSES = ['time1-morning', 'time2-earlyDay', 'time3-lateDay', 'time4-evening', 'time5-night', 'time6-lateNight'];
  TIMESTRINGS = ['Morning', 'Early Day', 'Day', 'Evening', 'Night', 'Late Night'];
  _dateToTimeFeatureByArray = function(dateString, array) {
    var date, hourPlayed;
    date = new Date(dateString);
    hourPlayed = date.getHours() + 1;
    switch (hourPlayed) {
      case 5:
      case 6:
      case 7:
      case 8:
        return array[0];
      case 9:
      case 10:
      case 11:
      case 12:
        return array[1];
      case 13:
      case 14:
      case 15:
      case 16:
        return array[2];
      case 17:
      case 18:
      case 19:
      case 20:
        return array[3];
      case 21:
      case 22:
      case 23:
      case 24:
        return array[4];
      case 1:
      case 2:
      case 3:
      case 4:
        return array[5];
      default:
        return null;
    }
  };
  View = ResultView.extend({
    className: 'emotionsHistory',
    events: {
      'click .emoticon': 'onClickEmoticon'
    },
    render: function() {
      var _this = this;
      if (this.collection == null) {
        console.error("Need a collection");
      }
      this.listenTo(this.collection, 'sync', this.renderChart);
      this.$el.append(tmpl);
      setTimeout((function() {
        return _this.$(_holderSel).scrollLeft(9999);
      }), _scrollFixDelay);
      return this;
    },
    renderChart: function() {
      if (!this.collection.length) {
        this.remove();
        return;
      }
      this.collection.each(function(item) {
        item.set('timeClass', _dateToTimeFeatureByArray(item.attributes.time_played, TIMECLASSES));
        return item.set('timeString', _dateToTimeFeatureByArray(item.attributes.time_played, TIMESTRINGS));
      });
      this.$el.html(_tmpl({
        results: this.collection.toJSON()
      }));
      return this.$(_emoticonSel).tooltip({
        placement: 'right',
        container: 'body',
        html: true,
        animation: false
      });
    },
    onClickEmoticon: function(e) {
      var id;
      id = $(e.target).data('id');
      return perch.show({
        title: 'Emotion Details',
        btn1Text: null,
        large: true,
        content: new EmotionsResultsView({
          model: new Backbone.Model(this.collection.get(id).attributes)
        })
      });
    }
  });
  return View;
});
