

define('dashboard/personality/core',['backbone', 'Handlebars', 'text!./core.hbs', 'dashboard/widgets/base', 'composite_views/perch', 'markdown', 'core', 'ui_widgets/share'], function(Backbone, Handlebars, tmpl, Widget, perch, markdown, app, ShareView) {
  var View, _className, _tmpl, _widgetSel;
  _widgetSel = '.widget';
  _className = 'corePersonality';
  _tmpl = Handlebars.compile(tmpl);
  View = Widget.extend({
    className: "doubleWide tall " + _className,
    events: {
      'click .widget': 'onClick'
    },
    render: function() {
      var share;
      this.$el.html(_tmpl(this.model.attributes.profile_description));
      share = new ShareView({
        data: {
          title: "My personality is '" + this.model.attributes.profile_description.name + "'",
          text: 'You can find out your personality, too, at https://alpha.tidepool.co',
          link: 'https://alpha.tidepool.co',
          image: "https://alpha.tidepool.co/images/badges/" + this.model.attributes.profile_description.logo_url
        }
      });
      this.$el.append(share.render().el);
      return this;
    },
    onClick: function() {
      var descHtml;
      descHtml = markdown.toHTML(this.model.attributes.profile_description.description);
      return perch.show({
        large: true,
        title: this.model.attributes.profile_description.name,
        msg: descHtml,
        btn1Text: 'Ok'
      });
    }
  });
  View.dependsOn = 'entities/my/personality';
  return View;
});
