

define('game/mini_instructions',['backbone'], function(Backbone) {
  var View;
  View = Backbone.View.extend({
    className: 'miniInstructions',
    initialize: function() {
      this.model = new Backbone.Model;
      return this.listenTo(this.model, 'change', this.render);
    },
    render: function() {
      this.$el.html(this.model.attributes.text);
      return this;
    }
  });
  return View;
});
