

define('dashboard/fitness/steps_and_emotions',['backbone', 'dashboard/widgets/base', 'text!./steps_and_emotions.hbs'], function(Backbone, Widget, tmpl) {
  var View, _widgetSel;
  _widgetSel = '.widget';
  View = Widget.extend({
    className: 'doubleWide stepsAndEmotions',
    render: function() {
      this.emotions = this.options.emotions;
      console.log({
        collection: this.collection.toJSON(),
        emotions: this.emotions.toJSON()
      });
      if (this.emotions.length) {
        this.renderChart();
      } else {
        this.remove();
      }
      return this;
    },
    renderChart: function() {
      return console.log('renderChart');
    }
  });
  View.dependsOn = {
    collection: {
      klass: 'entities/my/activities'
    },
    emotions: {
      klass: 'entities/results/results',
      fetchOptions: {
        data: {
          type: 'EmoResult'
        }
      }
    }
  };
  return View;
});
