

define('game/calculate_results',['backbone', 'Handlebars', 'text!./calculate_results.hbs', 'core', 'composite_views/perch', 'entities/daddy_ios'], function(Backbone, Handlebars, tmpl, app, perch, IOS) {
  var View, _me, _statusMsgSel;
  _me = 'game/calculate_results';
  _statusMsgSel = '#StatusMessage';
  View = Backbone.View.extend({
    className: 'calculateResults',
    initialize: function() {
      this.ios = new IOS;
      this.listenTo(this.model, 'change', this.onChange);
      this.listenTo(this.model, 'error', this.onError);
      return app.analytics.track(this.className, 'Starting game results calculation');
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    _updateStatusMsg: function(model) {
      if (model.attributes.status.message) {
        return this.$(_statusMsgSel).text(model.attributes.status.message);
      }
    },
    _showResults: function() {
      return app.router.navigate("gameResults/" + this.model.attributes.game_id, {
        trigger: true
      });
    },
    _gotGameResults: function(model) {
      app.analytics.track(this.className, 'Successfully calculated game results');
      if (this.ios.isUp) {
        return this.ios.holla(1);
      } else {
        return this._showResults();
      }
    },
    onError: function(model, xhr) {
      var _ref,
        _this = this;
      this.ios.holla(0);
      app.analytics.track(this.className, 'Error Getting game results');
      return perch.show({
        title: 'Sorry, There Is a Problem.',
        msg: ((_ref = xhr.responseJSON) != null ? _ref.status.message : void 0) || xhr.statusText,
        btn1Text: 'Ok',
        onClose: function() {
          return _this._showResults();
        },
        mustUseButton: true
      });
    },
    onChange: function(model) {
      this._updateStatusMsg(model);
      if (model.attributes.status.state === model.STATES.done) {
        return this._gotGameResults(model);
      }
    }
  });
  return View;
});
