

define('game/levels/proximity_takes_turns/billboard',['jquery', 'backbone', 'Handlebars'], function($, Backbone, Handlebars) {
  var View, _aboveClass, _animTime, _focusOutClass, _labelSel, _onDeckClass, _tmpl;
  _tmpl = Handlebars.compile("<label class='{{className}}'>{{trait1}} <span class='muted'>/</span> {{trait2}}</label>");
  _onDeckClass = 'onDeck';
  _focusOutClass = 'focusOut';
  _aboveClass = 'above';
  _animTime = 200;
  _labelSel = 'label';
  View = Backbone.View.extend({
    className: 'billboard',
    slideIn: function(model) {
      var $label;
      this.$el.append(_tmpl(model.attributes));
      $label = this.$(_labelSel);
      return setTimeout((function() {
        return $label.addClass(_onDeckClass);
      }), 1);
    },
    focusOut: function() {
      var $label;
      $label = this.$(_labelSel);
      $label.addClass(_focusOutClass);
      setTimeout((function() {
        return $label.remove();
      }), _animTime);
      return this;
    },
    focusIn: function(model) {
      var $label;
      $label = $(_tmpl(_.extend(model.attributes, {
        className: "" + _aboveClass + " " + _onDeckClass
      })));
      this.$el.append($label);
      return setTimeout((function() {
        return $label.removeClass(_aboveClass);
      }), 1);
    }
  });
  return View;
});
