

define('entities/careBears/facebook',['backbone', './careBear', 'core'], function(Backbone, CareBear, app) {
  var Model, _me;
  _me = 'entities/careBears/facebook';
  Model = CareBear.extend({
    defaults: {
      app_id: app.cfg.fbId,
      redirect_uri: "https://alpha.tidepool.co/" + window.location.hash
    },
    url: 'https://www.facebook.com/dialog/feed/',
    validate: function(attrs, options) {
      if (!attrs.name) {
        return "Need a name";
      }
      if (!attrs.caption) {
        return "Need a caption";
      }
      if (!attrs.link) {
        return "Need a link";
      }
      if (!attrs.picture) {
        console.warn("Facebook is better with .pictures");
      }
      return null;
    }
  });
  return Model;
});
