

define('game/levels/snoozer/clock_view',['underscore', 'backbone', 'Handlebars'], function(_, Backbone, Handlebars) {
  var CLASSES, View, _defaultDuration, _fallTime, _feedbackTime, _markup;
  _defaultDuration = 1000;
  _fallTime = 150;
  _feedbackTime = 500;
  CLASSES = {
    ring: 'ringing',
    decoy: 'decoy',
    float: 'float',
    hitGood: 'yay',
    hitBad: 'boo'
  };
  _markup = '<b class="clock"></b>';
  View = Backbone.View.extend({
    className: 'clockHolder',
    events: {
      click: 'onClick'
    },
    initialize: function() {
      if (!this.options.daddy) {
        throw new Error("Need options.daddy");
      }
      return _.bindAll(this, 'miss', '_stopRing');
    },
    render: function() {
      this.$el.html(_markup);
      return this;
    },
    _reset: function() {
      this.ringTime = new Date();
      this.$el.prop('class', this.className);
      return this.model.clear().set(this.model.defaults);
    },
    _clearTimeout: function() {
      return clearTimeout(this.timeout);
    },
    _upNext: function(action, delay) {
      clearTimeout(this.timeout);
      return this.timeout = setTimeout(action, delay);
    },
    _stopRing: function() {
      var _this = this;
      this._reset();
      return this._upNext((function() {
        return _this.isRinging = false;
      }), _fallTime);
    },
    correct: function() {
      this.model.set({
        reaction_time: (new Date()) - this.ringTime
      });
      this.options.daddy.trigger('correct', this.model);
      this.$el.addClass(CLASSES.hitGood);
      return this._upNext(this._stopRing, _feedbackTime);
    },
    incorrect: function() {
      this.options.daddy.trigger('incorrect', this.model);
      this.$el.addClass(CLASSES.hitBad);
      return this._upNext(this._stopRing, _feedbackTime);
    },
    miss: function() {
      this.model.set({
        reaction_time: (new Date()) - this.ringTime
      });
      if (!this.$el.hasClass(CLASSES.decoy)) {
        this.options.daddy.trigger('miss', this.model);
      }
      return this._stopRing();
    },
    onClick: function(e) {
      this._clearTimeout();
      if (this.$el.hasClass(CLASSES.decoy)) {
        return this.incorrect();
      } else if (this.$el.hasClass(CLASSES.ring)) {
        return this.correct();
      } else {
        return this.incorrect();
      }
    },
    ring: function(duration, isDecoy) {
      this._reset();
      duration = duration || _defaultDuration;
      this.model.set({
        duration: duration,
        isDecoy: isDecoy || false
      });
      if (!isDecoy) {
        this.options.daddy.trigger('ring', this.model);
      } else {
        this.options.daddy.trigger('decoy', this.model);
      }
      this.$el.addClass(CLASSES.ring);
      this.$el.addClass(CLASSES.float);
      if (isDecoy) {
        this.$el.addClass(CLASSES.decoy);
      }
      this.isRinging = true;
      return this._upNext(this.miss, duration);
    }
  });
  return View;
});
