

define('game/levels/circle_size/sizey_view',['backbone', 'Handlebars', './circle_view', 'text!./sizey_view.hbs', 'fastclick', 'jqueryui/slider'], function(Backbone, Handlebars, CircleView, tmpl, FastClick, JqSlider) {
  var View, _circleSel, _me, _needsNormalClickClass, _sliderHandleSel, _sliderSel;
  _me = 'game/levels/circle_proximity/sizey_view';
  _sliderSel = '.slider';
  _sliderHandleSel = '.ui-slider-handle';
  _circleSel = '.circle';
  _needsNormalClickClass = 'needsclick';
  View = Backbone.View.extend({
    className: 'sizey',
    tmpl: Handlebars.compile(tmpl),
    initialize: function() {
      _.bindAll(this, 'onSlide', 'onFocus', 'onClick');
      return this.circle = new CircleView({
        model: this.model
      });
    },
    render: function() {
      this.$el.html(this.circle.render().el);
      this.$el.append(this.tmpl(this.model.attributes));
      this.$el.find(_sliderSel).slider({
        value: this.model.get('size'),
        max: this.model.maxSize,
        slide: this.onSlide
      });
      this.$(_sliderHandleSel).on('click', this.onClick);
      this.$(_sliderHandleSel).on('focus', this.onFocus);
      return this;
    },
    onSlide: function(e, ui) {
      return this.model.set({
        size: ui.value,
        interacted: true
      });
    },
    onClick: function() {
      return this.model.set({
        interacted: true
      });
    },
    onFocus: function() {
      return this.model.set({
        interacted: true
      });
    }
  });
  return View;
});
