

define('ui_widgets/share',['backbone', 'Handlebars', 'text!./share.hbs', 'entities/careBears/facebook', 'entities/careBears/twitter', 'entities/careBears/mailto', 'core'], function(Backbone, Handlebars, tmpl, Facebook, Twitter, Email, app) {
  var Me, _btnSel, _me, _popoverSel;
  _me = 'ui_widgets/share';
  _popoverSel = 'nav';
  _btnSel = '.btn';
  Me = Backbone.View.extend({
    tagName: 'aside',
    className: 'share',
    events: {
      'click .action-facebook': 'onClickFacebook',
      'click .action-twitter': 'onClickTwitter',
      'click .action-email': 'onClickMailto'
    },
    render: function() {
      this.$el.html(tmpl);
      return this;
    },
    onClickFacebook: function(e) {
      var facebookShareBear;
      e.stopPropagation();
      facebookShareBear = new Facebook({
        picture: this.options.data.image,
        link: this.options.data.link,
        name: this.options.data.title,
        caption: this.options.data.text
      });
      facebookShareBear.save();
      return app.analytics.trackKeyMetric(this.className, 'clicked facebook');
    },
    onClickTwitter: function(e) {
      var twitterShareBear;
      e.stopPropagation();
      twitterShareBear = new Twitter({
        text: "" + this.options.data.title + " -- ",
        url: this.options.data.link
      });
      twitterShareBear.save();
      return app.analytics.trackKeyMetric(this.className, 'clicked twitter');
    },
    onClickMailto: function(e) {
      var email;
      e.stopPropagation();
      email = new Email({
        subject: this.options.data.title,
        body: this.options.data.text
      });
      email.save();
      return app.analytics.trackKeyMetric(this.className, 'clicked mailto');
    }
  });
  return Me;
});
