

define('entities/careBears/mailto',['backbone', './careBear'], function(Backbone, CareBear) {
  var Model, _me;
  _me = 'entities/careBears/mailto';
  Model = CareBear.extend({
    url: 'mailto:',
    validate: function(attrs, options) {
      if (!attrs.subject) {
        return "Need subject";
      }
      if (!attrs.body) {
        return "Need body";
      }
    }
  });
  return Model;
});
