

define('preferences/personalization',['jquery', 'underscore', 'backbone', 'Handlebars', 'entities/preferences/training-descriptions', 'entities/preferences/training', 'text!./personalization.hbs', 'text!./personalization-games.hbs', 'ui_widgets/formation', 'ui_widgets/hold_please', 'ui_widgets/psst', 'composite_views/perch'], function($, _, Backbone, Handlebars, TrainingPreferencesDesc, TrainingPreferences, tmpl, tmplGames, Formation, holdPlease, psst, perch) {
  var ProfileDialog, _btnSel, _categories, _completedClass, _contentSel, _explanationSel, _explanationTmpl, _infoSel, _infoTips, _infoTmpl, _pageCount, _stepsSel, _titles, _tmpl;
  _btnSel = '.actionZone .btn';
  _contentSel = '.content';
  _stepsSel = '.stepsRemainingWizard li';
  _completedClass = 'complete';
  _tmpl = Handlebars.compile(tmpl);
  _pageCount = 2;
  _titles = ['1. Productivity', '2. Mood'];
  _categories = ['productivity', 'mood'];
  _infoTips = ['TidePool\'s patented exercises are designed by top American psychologists.', 'Many TidePool users report positive results and also shared TidePool with friends and family.', 'TidePool games will improve your self-awareness and coach you to achieve your goals.'];
  _infoSel = '.footer';
  _infoTmpl = Handlebars.compile('<i class="icon-info-sign"></i> &nbsp; {{text}}');
  _explanationSel = '.explanation';
  _explanationTmpl = Handlebars.compile('<h3>{{title}}</h3><p class="small">{{subtitle}}</p>');
  ProfileDialog = Backbone.View.extend({
    className: 'personalization',
    events: {
      'click #ActionNext': 'onClickNext',
      'submit form': 'onSubmit',
      'input change': 'onInputChange',
      'click .games a': 'onGameClicked'
    },
    initialize: function() {
      var promise;
      this.data = new TrainingPreferences();
      this.fields = new TrainingPreferencesDesc();
      promise = $.when(this.data.fetch(), this.fields.fetch());
      promise.done(_.bind(this.onSync, this));
      promise.fail(this.onError);
      return this.curPage = 0;
    },
    render: function() {
      holdPlease.show(this.$el);
      return this;
    },
    renderContents: function() {
      holdPlease.hide(this.$el);
      this.$el.html(_tmpl);
      return this._showFields(this.curPage);
    },
    onSync: function(dataResp, fieldsResp) {
      if (this.data.attributes) {
        this.fields.setValues(this.data.attributes.data);
      }
      return this.renderContents();
    },
    onError: function(model, xhr) {
      var msg;
      if (xhr.status === 0) {
        msg = 'Unknown Server Error';
      } else {
        msg = "" + xhr.status + ": " + xhr.statusText;
      }
      return perch.hide();
    },
    _showFields: function(idx) {
      this.options.app.analytics.track('Personalizations', "Showing Step: " + idx);
      this._indexActions(idx);
      this.$(_explanationSel).html(_explanationTmpl({
        title: _titles[idx],
        subtitle: 'Select all options that you are interested to improve.'
      }));
      this.form = new Formation({
        data: this.fields.where({
          category: _categories[idx]
        })
      });
      this.$(_contentSel).html(this.form.render().el);
      return this;
    },
    _indexActions: function(idx) {
      this.$(_stepsSel).eq(idx).addClass(_completedClass);
      return this.$(_infoSel).html(_infoTmpl({
        text: _infoTips[idx]
      }));
    },
    _showComplete: function(idx) {
      this.options.app.analytics.track('Personalizations', "Completed");
      this._indexActions(idx);
      this.$(_explanationSel).html(_explanationTmpl({
        title: 'Your Personalized Program is ready!',
        subtitle: 'You can go back at any time and change your preferences.'
      }));
      this.$(_contentSel).html(tmplGames);
      this.$(_btnSel).remove();
      return this;
    },
    onClickNext: function() {
      var _ref;
      this.curPage++;
      this.fields.setValues(this.form.getVals());
      this.data.setValues(this.form.getVals());
      if ((_ref = this.form) != null) {
        _ref.remove();
      }
      if (this.curPage === _pageCount) {
        this.data.save();
        return this._showComplete(this.curPage);
      } else {
        return this._showFields(this.curPage);
      }
    },
    onGameClicked: function() {
      return perch.hide();
    }
  });
  return ProfileDialog;
});
