

define('dashboard/action_items',['underscore', 'backbone', 'Handlebars', 'text!./action_items.hbs', 'entities/my/actions'], function(_, Backbone, Handlebars, tmpl, NextAction) {
  var TYPETOICON, View, _greetings, _tmpl;
  _greetings = ['Hello', 'Hi', 'Aloha', 'Bonjour', 'Hola', 'Hallo', 'Ahoy', 'G\'Day', 'Howdy', 'Hiya'];
  Handlebars.registerHelper('pickOne', function(array) {
    return array[Math.floor(Math.random() * array.length)];
  });
  _tmpl = Handlebars.compile(tmpl);
  TYPETOICON = {
    Website: 'gfx-recommend_website',
    Video: 'gfx-recommend_video',
    Course: 'gfx-recommend_course',
    Comic: 'gfx-recommend_comic',
    Book: 'gfx-recommend_book',
    App: 'gfx-recommend_app',
    Activity: 'gfx-recommend_activity',
    ReactionTime: 'gfx-reactiontime',
    Emotions: 'gfx-emotiongame',
    Preferences: 'icon-cog'
  };
  View = Backbone.View.extend({
    className: 'actionItems',
    events: {
      'click a': 'onClick'
    },
    initialize: function() {
      this.collection = new NextAction();
      this.listenTo(this.collection, 'sync', this.onSync);
      return this.collection.fetch();
    },
    render: function() {
      var data, first;
      first = this.collection.at(0);
      data = _.extend(first.attributes, {
        greetings: _greetings
      });
      data.icon = TYPETOICON[data.link_type];
      this.$el.html(_tmpl(data));
      return this;
    },
    onSync: function() {
      return this.render();
    },
    onClick: function(e) {
      var $a, title;
      $a = $(e.currentTarget);
      title = $a.prop('href');
      if (title && this.options.app) {
        return this.options.app.analytics.track('Action Items', "Clicked " + title);
      }
    }
  });
  return View;
});
