

define('dashboard/personality/recommendation',['backbone', 'Handlebars', 'dashboard/widgets/base', 'text!./recommendation.hbs', 'text!./recommendation-details.hbs', 'composite_views/perch', 'core'], function(Backbone, Handlebars, Widget, tmpl, tmplDetails, perch, app) {
  var View, _className, _recLinkSel, _tmplDetails;
  _className = 'recommendation';
  _recLinkSel = '#ActionRecommendationLink';
  _tmplDetails = Handlebars.compile(tmplDetails);
  View = Widget.extend({
    className: _className,
    events: {
      'click .widget': 'onClick'
    },
    render: function() {
      var data;
      this.$el.html(tmpl);
      data = this.model.attributes;
      switch (data.link_type) {
        case 'Book':
          data.icon = 'icon-book';
          break;
        case 'App':
          data.icon = 'icon-cloud-download';
          break;
        case 'Video':
          data.icon = 'icon-youtube-play';
          break;
        case 'Comic':
          data.icon = 'icon-smile';
          break;
        case 'Lecture':
          data.icon = 'icon-bullhorn';
      }
      this._detailsMarkup = _tmplDetails(data);
      return this;
    },
    onSync: function(model) {
      if (model.attributes.sentence) {
        return this.render();
      }
    },
    onClick: function() {
      if (!this._detailsMarkup) {
        return;
      }
      perch.show({
        content: this._detailsMarkup,
        btn1Text: null,
        supressTracking: true,
        large: true
      });
      app.analytics.track(_className, 'Shown');
      return $(_recLinkSel).one('click', this.onReccClick);
    },
    onReccClick: function() {
      return app.analytics.track(_className, 'External Link Clicked');
    }
  });
  View.dependsOn = 'entities/my/recommendations';
  return View;
});
