

define('classes/model',['underscore', 'backbone'], function(_, Backbone) {
  var Model;
  Model = Backbone.Model.extend({
    dewrap: function(resp, options) {
      if (_.isObject(resp.data) && _.isObject(resp.status)) {
        return resp.data;
      } else {
        return resp;
      }
    },
    parse: function(resp, options) {
      resp = this.dewrap(resp, options);
      return resp;
    },
    increment: function(property) {
      var val;
      val = this.get(property);
      if (isNaN(val)) {
        return;
      }
      return this.set(property, val + 1);
    }
  });
  return Model;
});
