

define('game/levels/emotions_circles/main',['underscore', 'backbone', 'game/levels/_base', 'entities/circles', 'game/levels/circle_proximity'], function(_, Backbone, Level, Circles, CircleProximity) {
  var View;
  View = Level.extend({
    className: 'emotionsCircles',
    start: function() {
      this.options.instructions.set({
        text: this.model.get('instructions')
      });
      this.collection = new Circles(this.model.attributes.circles);
      this.collection.each(function(model) {
        return model.set('size', 4);
      });
      this.clearInteracted(this.collection);
      return this.track(Level.EVENTS.start);
    },
    render: function() {
      var _this = this;
      this.circleProximity = new CircleProximity({
        collection: this.collection,
        runner: this,
        showInstructions: this.options.showInstructions
      });
      this.$el.html(this.circleProximity.el);
      setTimeout((function() {
        return _this.circleProximity.render();
      }), 1);
      this.listenToOnce(this.circleProximity, 'done', this.onTestDone);
      return this;
    },
    onTestDone: function() {
      this.summaryData = {
        data: this.collection.toJSON(),
        self_coord: {
          top: this.circleProximity.selfView.getSelfCenter().y - this.circleProximity.selfView.getSelfRadius(),
          left: this.circleProximity.selfView.getSelfCenter().x - this.circleProximity.selfView.getSelfRadius(),
          size: this.circleProximity.selfView.getSelfRadius() * 2
        }
      };
      return this.endLevel();
    }
  });
  return View;
});

define('game/levels/emotions_circles', ['game/levels/emotions_circles/main'], function (main) { return main; });
