

define('dashboard/career/skills',['backbone', 'dashboard/widgets/lister'], function(Backbone, Lister) {
  var View;
  View = Lister.extend({
    className: 'coolTones',
    render: function() {
      this.renderList({
        title: 'Skills to Work On',
        icon: 'icon-ok',
        list: this.model.attributes.skills
      });
      return this;
    }
  });
  View.dependsOn = 'entities/my/career';
  return View;
});
