

define('game/results/personality',['backbone', 'Handlebars', 'game/results/base', 'text!./personality.hbs', 'markdown'], function(Backbone, Handlebars, ResultView, tmpl, markdown) {
  var View, _me;
  _me = 'game/results/personality';
  View = ResultView.extend({
    className: 'personality',
    tmpl: Handlebars.compile(tmpl),
    start: function() {
      return this._prepareData();
    },
    _prepareData: function() {
      var score;
      score = this.model.get('score');
      score.one_liner = markdown.toHTML(score.one_liner);
      this.model.set('score', score);
      return this;
    }
  });
  return View;
});
