

define('entities/careBears/twitter',['backbone', './careBear'], function(Backbone, CareBear) {
  var Model, _me;
  _me = 'entities/careBears/twitter';
  Model = CareBear.extend({
    defaults: {
      related: 'TidePoolInc'
    },
    url: 'http://twitter.com/share',
    validate: function(attrs, options) {
      if (!attrs.text) {
        return "Need text";
      }
      if (!attrs.url) {
        return "Need url";
      }
    }
  });
  return Model;
});
