

define('game/levels/alex_trebek/main',['underscore', 'backbone', 'game/levels/_base', 'ui_widgets/icon_slider', 'ui_widgets/formation/select_by_icon'], function(_, Backbone, Level, IconSlider, SelectByIcon) {
  var TYPES, TYPEVIEWS, View, _me;
  _me = 'game/levels/alex_trebek';
  TYPES = {
    icon_slider: 'icon_slider',
    select_by_icon: 'select_by_icon'
  };
  TYPEVIEWS = {
    icon_slider: IconSlider,
    select_by_icon: SelectByIcon
  };
  View = Level.extend({
    className: 'alexTrebek',
    render: function() {
      var _this = this;
      this.$el.empty();
      if (this.collection != null) {
        this.collection.each(function(question) {
          return _this._renderOneQuestion(question);
        });
      }
      return this;
    },
    _renderOneQuestion: function(question) {
      var questionView, type;
      type = question.attributes.question_type;
      if (TYPEVIEWS[type]) {
        questionView = new TYPEVIEWS[type]({
          model: question
        });
        this.$el.append(questionView.render().el);
        switch (type) {
          case TYPES.icon_slider:
            this.listenTo(questionView, 'slide', this.onSlide);
            return this.listenTo(questionView, 'stop', this.onChange);
          case TYPES.select_by_icon:
            return this.listenTo(questionView, 'change', this.onChange);
        }
      } else {
        return console.warn("" + type + " is not a known question type");
      }
    },
    _setDataForServer: function() {
      return this.summaryData = {
        data: this.collection.toJSON()
      };
    },
    onSlide: function(data) {
      return this.track(Level.EVENTS.interact, data.model.attributes);
    },
    onChange: function(data) {
      data.model.set({
        'interacted': true
      });
      data.model.set('answer', data.value);
      this.track(Level.EVENTS.change, data.model.attributes);
      if (this.checkAllInteracted(this.collection)) {
        this.readyToProceed();
      }
      return this._setDataForServer();
    }
  });
  return View;
});

define('game/levels/alex_trebek', ['game/levels/alex_trebek/main'], function (main) { return main; });
